.main-footer {
	margin-top: 20px;
	padding-top: 12px;
	padding-bottom: 60px;

	.footnote {
		color: $footnote-text-color;
	}

	&.has-border {
		border-top: 1px solid $hr-dark;
	}

	nav {
		@include respond-to("large") {
			margin-top: 20px;
		}
	}

	.icons-globe-filled {
		position: relative;
		font-size: 34px;
		top: -2px;
		left: 8px;
		color: #606b78;
	}

	.list-inline.pipe-separator.text-medium .text-primary {
		@include respond-to("medium") {
			border: none;
		}
	}

	.text-child {
		& + .text-child {
			border-left: 1px solid;
			margin-left: 7px;
			padding-left: 7px;
			@include respond-to("small") {
				margin: 0;
				padding: 0;
				border: none;
			}
		}

		@include respond-to("small") {
			display: block;
			float: left;
			width: 100%;
		}
	}

	.icons-feed {
		color: $footer-icon-feed-color;
	}

	&.dark-bg .back-link {
		color: $back-link-color;
	}
}

.main-footer .icons-slogan-small svg,
.footer-logos .icons-slogan-small svg {
	fill: $footer-slogan-bgc;
}

.quick-access {
	margin-bottom: 20px;

	ul li {
		line-height: 18px;
	}

	@include respond-to("large") {
		ul li a {
			list-height: 22px;
		}
	}
}

.quick-access, .worldmap-highlight {
	.h6, p {
		font-size: 15px;
		line-height: 26px;
		margin-bottom: 4px;
	}

	a {
		font-size: 11px;
	}
}

.worldmap-highlight {
	p {
		font-size: 11px;
		line-height: 16px;
	}

	@include respond-to("large") {
		.hide-lg + div {
			padding-left: 0;
		}
	}

	figure svg {
		.st0 {
			fill:#646d77;
			stroke:#646d77;
			stroke-width:1;
		}
	}
}

.footer-logos {
	margin-top: 4px;
	margin-bottom: 10px;
	padding-top: 20px;
	border-top: 1px solid $footer-border-color;
	border-bottom: 1px solid $footer-border-color;

	a {
		line-height: 1.4;
  		font-size: 11px;

  		&:hover {
  			text-decoration: none;
  		}
	}
}

.footer-icons {
	height: 38px;
	margin-bottom: 20px;
}

.footer-disclaimer {
	margin-top: 30px;
	font-size: 11px;
}

.contacts {
	.icons-phone {
		position: relative;
		top: 3px;
	}
}


/**
 * --CIO--
 * @not-need-when: all footer has same father class
 */
.footer-notes {
	.pipe-separator li,
	.pipe-separator a {
		color: $footer-link-color;
	}

	.icons-feed {
		margin-right: 3px;
		color: $footer-icon-feed-color;
	}
}
