//
// Padding
//
.no-padding {
	padding: 0 !important;
}

.no-padding-top {
	padding-top: 0 !important;
}

.no-padding-left {
	padding-left: 0 !important;
}

.no-padding-right {
	padding-right: 0 !important;
}

.no-padding-bottom {
	padding-bottom: 0 !important;
}

// md
.no-padding-md {
	@include respond-to("medium") {
		padding: 0 !important;
	}
}

.no-padding-right-md {
	@include respond-to("medium-and-up") {
		padding-right: 0 !important;
	}
}

.no-padding-xl {
	@include respond-to("xlarge") {
		padding: 0 !important;
	}
}

.no-padding-lg {
	@include respond-to("up-than-large") {
		padding: 0 !important;
	}
}

.no-padding-left-md {
	@include respond-to("medium-and-up") {
		padding-left: 0 !important;
	}
}


//
// Margin
//
.no-margin {
	margin: 0 !important;
}

.no-margin-top {
	margin-top: 0 !important;
}

.no-margin-bottom {
	margin-bottom: 0 !important;
}

.no-margin-left {
	margin-left: 0 !important;
}

.no-margin-right {
	margin-right: 0 !important;
}


//
// Pulls
//
.pull-left {
	float: left !important;
}

.pull-right {
	float: right !important;
}

.pull-left-lg {
	@include respond-to("large") {
		float: left !important;
	}
}

.pull-right-lg {
	@include respond-to("large") {
		float: right !important;
	}
}

.pull-left-md {
	@include respond-to("medium") {
		float: left !important;
	}
}

.pull-right-md {
	@include respond-to("medium") {
		float: right !important;
	}
}

.pull-left-sm {
	@include respond-to("small") {
		float: left !important;
	}
}

.pull-right-sm {
	@include respond-to("small") {
		float: right !important;
	}
}

// For modern browsers
.clearfix:before,
.clearfix:after {
	content: "";
	display: table;
}

.clearfix:after {
	clear: both;
}

// For IE 6/7 (trigger hasLayout)
.clearfix {
	*zoom: 1;
}

.clear {
	clear: both;
}


//
// Breaks
//
.break-lg {
	display: inline;

	@include respond-to("large") {
		display: block;
		width: 100%;
		height: 1px;
		margin: 0 0 -1px;
		clear: both;
	}
}

.break-md {
	display: inline;

	@include respond-to("medium") {
		display: block;
		width: 100%;
		height: 1px;
		margin: 0 0 -1px;
		clear: both;
	}
}

.break-sm {
	display: inline;

	@include respond-to("small") {
		display: block;
		width: 100%;
		height: 1px;
		margin: 0 0 -1px;
		clear: both;
	}
}


//
// Clear
//
.clearfix:before,
.clearfix:after {
	content: "";
	display: table;
}

.clearfix:after {
	clear: both;
}

// For IE 6/7 (trigger hasLayout)
.clearfix {
	*zoom: 1;
}

.clear {
	clear: both;
}

.clear-sm {
	@include respond-to("small") {
		clear: both !important;
	}
}

.clear-lg {
	@include respond-to("large") {
		clear: both !important;
	}
}

.clear-md {
	@include respond-to("medium") {
		clear: both !important;
	}
}

.clear-sm {
	@include respond-to("small") {
		clear: both !important;
	}
}


//
// Hide
//
.hide {
	display: none !important;
}
.hide-lg {
	@include respond-to("large") {
		display: none !important;
	}
}
.hide-md {
	@include respond-to("medium") {
		display: none !important;
	}
}
.hide-sm {
	@include respond-to("small") {
		display: none !important;
	}
}
.visible-lg {
	@include respond-to("large") {
		display: block !important;
	}
}

.visible-md {
	@include respond-to("medium") {
		display: block !important;
	}
}

.visible-sm {
	@include respond-to("small") {
		display: block !important;
	}
}


//
// TB
//
.tb-margin {
	margin: 1em 0 !important;
}

.tb-margin-half {
	margin: 0.5em 0 !important;
}

.no-break {
	white-space: nowrap;
}

.align-right,
%align-right {
	text-align: right !important;
}

.align-center,
%align-center {
	text-align: center;
}

.aux-v-align-middle {
	vertical-align: middle;
}

.aux-v-align-top {
	vertical-align: top;
}

/*
 * Hide only visually, but have it available for screenreaders: h5bp.com/v
 */

.visuallyhidden {
	border: 0;
	clip: rect(0 0 0 0);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
}

/*
 * Extends the .visuallyhidden class to allow the element to be focusable
 * when navigated to via the keyboard: h5bp.com/p
 */

.visuallyhidden.focusable:active,
.visuallyhidden.focusable:focus {
	clip: auto;
	height: auto;
	margin: 0;
	overflow: visible;
	position: static;
	width: auto;
}
.mobile-only {
	@extend .visuallyhidden;
	@include respond-to("large") {
		border: inherit;
		clip: rect(0 0 0 0);
		height: auto;
		margin: inherit;
		overflow: inherit;
		padding: inherit;
		position: inherit;
		width: auto;
	}
}

.gradient-to-top {
	width:100%;
	height:90%;
	position:absolute;
	bottom:0;
	left:0;
	background: -moz-linear-gradient(bottom, rgba(0,0,0,1) 30%, rgba(0,0,0,0.4%) 70%, rgba(0,0,0,0) 100%); /* FF3.6+ */
	background: -webkit-gradient(linear, left bottom, left bottom, color-stop(0%,rgba(0,0,0,1)), color-stop(70%,rgba(0,0,0,0.4%)), color-stop(100%,rgba(0,0,0,0))); /* Chrome,Safari4+ */
	background: -webkit-linear-gradient(bottom, rgba(0,0,0,1) 30%,rgba(0,0,0,0.4%) 70%,rgba(0,0,0,0) 100%); /* Chrome10+,Safari5.1+ */
	background: -o-linear-gradient(bottom, rgba(0,0,0,1) 30%,rgba(0,0,0,0.4%) 70%,rgba(0,0,0,0) 100%); /* Opera 11.10+ */
	background: -ms-linear-gradient(bottom, rgba(0,0,0,1) 30%,rgba(0,0,0,0.4%) 70%,rgba(0,0,0,0) 100%); /* IE10+ */
	background: linear-gradient(to top, rgba(0,0,0,1) 30%,rgba(0,0,0,0.4%) 70%,rgba(0,0,0,0) 100%); /* W3C */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#000000',GradientType=0 ); /* IE6-9 */
	z-index: 1;
	@include respond-to("small") {
		height:100%;
		background: -moz-linear-gradient(bottom, rgba(0,0,0,1) 50%, rgba(0,0,0,0.4%) 70%, rgba(0,0,0,0) 100%); /* FF3.6+ */
		background: -webkit-gradient(linear, left bottom, left bottom, color-stop(0%,rgba(0,0,0,1)), color-stop(70%,rgba(0,0,0,0.4%)), color-stop(100%,rgba(0,0,0,0))); /* Chrome,Safari4+ */
		background: -webkit-linear-gradient(bottom, rgba(0,0,0,1) 50%,rgba(0,0,0,0.4%) 70%,rgba(0,0,0,0) 100%); /* Chrome10+,Safari5.1+ */
		background: -o-linear-gradient(bottom, rgba(0,0,0,1) 50%,rgba(0,0,0,0.4%) 70%,rgba(0,0,0,0) 100%); /* Opera 11.10+ */
		background: -ms-linear-gradient(bottom, rgba(0,0,0,1) 50%,rgba(0,0,0,0.4%) 70%,rgba(0,0,0,0) 100%); /* IE10+ */
		background: linear-gradient(to top, rgba(0,0,0,1) 50%,rgba(0,0,0,0.4%) 70%,rgba(0,0,0,0) 100%); /* W3C */
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#000000',GradientType=0 ); /* IE6-9 */
	}
}

.symmetric-vert {
	overflow: hidden;

	> [class*="col-"] {
		padding-bottom: 9999px !important;
		margin-bottom: -9999px !important;
	}

	.symmetric-content {
		padding-bottom: 30px;
	}
}

.gpu-on {
	-webkit-transform: translate3d(0, 0, 0);
	-moz-transform: translate3d(0, 0, 0);
	-ms-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
}

.bg-image {
	position: relative;
	top: 0;
	left: 0;
	width: 100%;
	height: auto;
}

.liquid-image {
	width: 100%;
}

.fixed-el {
	position: fixed
}

.relative-el {
	position: relative;
}

.absolute-el {
	position: absolute;
}

.toggle-animation {
	@include vendor-prefix(transition, max-height 0.3s ease);
	@include vendor-prefix(transform, translate3d(0,0,0));
}

.toggle-wrap {
	max-height: $line-height-base * 3;
	overflow: hidden;

	&.active {
		max-height: 2000px;
	}
}


//
// Others
//
.center-block {
	float: none;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.block-it {
	display: block;
}

.space {
	clear: both;
	max-height: 1px;
	min-height: 1px;
	margin: 12px 0;
	hasLayout: 1;
}

.hr {
	background-color: $hr-dark;
}

.internal-concept {
	.hr {
		background-color: $hr-content;
	}
}

.hr--light {
	background-color: #FFF;
}

.hidden {
	visibility: hidden;
}

$spaces: 2, 4, 5, 6, 7, 8, 9, 10, 12, 14, 15, 16, 18, 20, 24, 25, 30, 100;

@each $i in $spaces {
	.space-#{$i} {
		clear: both;
		$ib: $i - 1;
		max-height: 1px;
		min-height: 1px;
		margin: #{$i}px 0 #{$ib}px;
		hasLayout: 1;
	}
}

.print-only {
	display: none;
}
