.performance-chart {
	-ms-box-sizing: content-box;
	-moz-box-sizing: content-box;
	-webkit-box-sizing: content-box;
	box-sizing: content-box;
}

.highcharts-range-selector {
	-ms-box-sizing: content-box !important;
	-moz-box-sizing: content-box !important;
	-webkit-box-sizing: content-box !important;
	box-sizing: content-box !important;

	&:focus {
		outline: solid 1px $link-color;
	}
}

.highcharts-input-group {
	g {
		rect {
			fill: white;
		}
	}
}

// donut charts
.chart-wrapper {
	position: relative;

	.aux-pos-top {
		margin-bottom: -35px;
		margin-top: 5px;
		position: relative;
		z-index: 1;
	}
}

.pie-chart-text-wrapper {
	$width: 160px;
	width: $width;
	position: absolute;
	left: 50%;
	top: 0;
	text-align: center;

	margin-left: $width / 2 * -1;

	.pie-chart-text {
		position: relative;
		display: block;
		text-align: center;

		span {
			font-size: 20px;
			line-height: 26px;
			color: $text-color;

			&.big {
				font-size: 30px;
			}
		}
	}
}

.legend-text, .chart-data-legend {
	font-size: 12px;
	color: #787d82;
	font-family: $basic-font;
	font-weight: normal;
}

.legend-percentage {
	font-size: 16px;
	font-weight: bold;
}

/**
 *
 */
$namespace: "donut-chart";

$width: 267px;
$padding: 20px;

/* @note: old cio-view layout worked without box-sizing: borderbox */
.highcharts-legend-item,
.donut-chart-data-legend {
	box-sizing: content-box !important;
}

.#{$namespace}-data-legend {
	$padding: 40px;
	display: block;
	width: $width - $padding;
	height: 18px;
	color: #787d82;
	padding: 8px 0 0 $padding;
	line-height: 1;
	border-bottom: 1px solid #c7c7c7;
}

.#{$namespace}-main-legend {
	color: white;
	width: $width - $padding;
	height: 26px;
	padding: 0 0 0 $padding;
	line-height: 26px;
	border-bottom: none;
	i {
		position: absolute;
		top: 9px;
		right: 20px;
	}
}

.donut-chart-main-legend {
	padding: 0 ($padding / 2) 0 ($padding / 2);
}

.#{$namespace}-legend-icon {
	position: absolute;
	top: 9px;
	left: 20px;
	display: block;
	width: 12px;
	height: 12px;
}

.#{$namespace}-legend-percentage {
	position: absolute;
	top: 8px;
	right: 20px;
}

.#{$namespace}-no-row {
	border: none;
}


.small-chart {
	.highcharts-legend-item {
		height: 25px;
	}
	.#{$namespace}-data-legend {
		$padding: 40px;
		display: block;
		width: 0;
		height: auto;
		color: #787d82;
		padding: 0;
		line-height: 1;
		border-bottom: 0;
		overflow: hidden;
		.#{$namespace}-legend-icon {
			display: none;
		}
	}
	.#{$namespace}-main-legend {
		$padding: 20px;
		display: inline;
		color: white;
		width: auto;
		height: auto;
		padding: 0 4px;
		line-height: 1;
		border-bottom: none;
		i {
			position: absolute;
			top: 9px;
			right: 20px;
		}
		.#{$namespace}-legend-icon {
			display: block;
		}
	}
	.#{$namespace}-legend-icon {
		position: absolute;
		top: 3px;
		left: 0px;
		width: 12px;
		height: 12px;
	}
	.#{$namespace}-legend-percentage {
		display: none;
	}
}

.highcharts-container span {
	font-weight: normal !important;
    font-family: UniversDeutscheBank,Helvetica,Arial,sans-serif !important;
    line-height: 1.2 !important;
}

.donut-chart-data-legend.donut-chart-main-legend {
	line-height: 26px !important;
}
