$article-aside-bgc: $white !default;
$article-aside-author-link-color: $text-color !default;
$article-aside-tags-wrapper-border-color: $content-hr !default;
$article-aside-width: 242px !default;

.article-aside {
	padding: 20px;
	background: $article-aside-bgc;

	&__item {
		margin-bottom: 20px;

		&:last-child {
			margin-bottom: 0;
		}
	}

	&__headline {
		display: block;
		font-weight: bold;
		margin-bottom: 14px;
		margin-top: 6px;
		line-height: 0.8;
	}

	&__author-photo {
		margin-right: 8px;
		float: left;
	}

	&__author-name {
		display: inline-block;
		max-width: 70%;
		margin-bottom: 0;
		font-size: 11px;
		line-height: 1.4;
	}

	&__author-link {
		display: inline-block;
		font-size: 11px;
		color: $article-aside-author-link-color;
		margin-top: 4px;
	}

	&__meta {
		float: left;
	}

	@include respond-to("large") {
		padding: 9px 17px 16px 17px;
	}

	@include respond-to("up-than-large") {
		padding-top: 24px;

		&__item {
			margin-bottom: 4px;
		}

		&__headline {
			margin-bottom: 19px;
		}

		&__author-name {
			font-size: 14px;
			margin-top: 10px;
		}

		&__author-photo {
			margin-top: 14px;
		}

		&__author-link {
			position: relative;
			top: -6px;
			line-height: 1.2;
		}

		&__meta {
			width: 146px;
		}

		.rate__icons {
			float: left;
		}

		.tags-wrapper {
			padding-top: 20px;
			padding-bottom: 14px;
			border-top: 1px solid $article-aside-tags-wrapper-border-color;
			border-bottom: 1px solid $article-aside-tags-wrapper-border-color;

		}

		.tags-wrapper .js-expand {
			width: 100%;
		}

		.tags-wrapper .icons-tag {
			// .icons-tag isn't
			top: 21px;
			left: 2px;
		}
	}


	// State fixed
	&__container {
		width: 100%;
		z-index: 2;
		@include vendor-prefix(transform, translate3d(0, 0, 0));

		&.fixed-el.bottom {
			position: absolute;
			top: auto !important;
			bottom: 0;
		}
	}


	// Related
	.tags-wrapper  {
		margin-bottom: 3px;

		.tags-wrapper .js-expand {
			text-align: right;
		}

		@include respond-to("up-than-large") {
			margin-bottom: 6px;
		}
	}
}


