$video-button-hover: $amber-2 !default;

.videos-wrapper {
	width: 100%;
	max-width: 566px;
	max-height: 318px;
}

.video-player-wrapper {
	position: relative;
	width: 100% !important;
	height: 100%;
	padding-top: 56.25%; // keep 16:9 aspect ratio
	box-sizing: border-box;

	.video-js {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}

* html .video-player-wrapper {
	width: 100%;
	height: 100%;
}

.video-player-screenlocker {
	background-color: $white;
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;

	.loader {
		position: absolute;
		top: 50%;
		left: 50%;
		margin-left: -5.5em;
		margin-top: -5.5em;
	}
}

.video-js {
	.vjs-big-play-button {
		@extend .btn-video;
		top: 100% !important;
		left: 100% !important;
		margin: -49px auto auto -72px !important;
		border-radius: 0 !important;
		opacity: 1 !important;

		&:before {
			width: auto !important;
		}

		&:hover, &:focus, &:active {
			box-shadow: none !important;
			background-color: $video-button-hover !important;
		}
	}
}

.video-player-wrapper .video-js .vjs-control-bar {
	display: none;
	opacity: 0;
}

.video-player-wrapper .video-js.vjs-has-started .vjs-control-bar {
	display: -webkit-box;
	display: -moz-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	opacity: 1;
}

.video-player-wrapper .video-js .vjs-menu .vjs-menu-content {
	-webkit-transform: translate(-5.5px,-32px) rotate(-90deg);
	-moz-transform: translate(-5.5px,-32px) rotate(-90deg);
	-ms-transform: translate(-5.5px,-32px) rotate(-90deg);
	-o-transform: translate(-5.5px,-32px) rotate(-90deg);
	transform: translate(-5.5px,-32px) rotate(-90deg);
}

.video-player-wrapper embed,
.video-player-wrapper object,
.BrightcoveExperience {
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
}

@include ie9 {
	.video-js {
		min-width: 100% !important;
		min-height: 100% !important;
		.vjs-fullscreen-control {
			display: none;
		}
		.vjs-progress-control {
			.vjs-progress-holder {
				.vjs-load-progress {
					margin-top: -15px !important;
				}
				.vjs-play-progress {
					margin-top: -13px !important;
				}
			}
		}
	}
	.video-player-wrapper {
		padding-top: 0 !important;
	}
}

@include ie10 {
	.video-js {
		min-width: 100% !important;
		min-height: 100% !important;
		.vjs-fullscreen-control {
			display: none;
		}
		.vjs-progress-control {
			.vjs-progress-holder {
				.vjs-load-progress {
					margin-top: -15px !important;
				}
				.vjs-play-progress {
					margin-top: -13px !important;
				}
			}
		}
	}
	.video-player-wrapper {
		padding-top: 0 !important;
	}
}

.video-js {
	.vjs-loading-spinner {
		&:before {
			left: 0;
		}
	}

	.vjs-control-bar {
		background-color: #000000 !important;
	}

	.vjs-progress-control {
		$progress-height: 13px;
		.vjs-progress-holder {
			padding: 0;
			height: $progress-height;
			top: 50%;
			margin-top: -7px;

			&:before {
				height: $progress-height;
				margin-top: 0;
				top: 0;
			}

			.vjs-load-progress {
				margin-top: 0;
				height: $progress-height;
			}

			.vjs-play-progress {
				height: $progress-height;
				margin-top: $progress-height * -1;
			}

			.vjs-seek-handle {
				height: $progress-height;
			}
		}
	}

	&.vjs-user-inactive {
		.vjs-progress-control {
			@include vendor-prefix(transform, translateY(-22px) !important);
		}
	}
}
