.tabs,
.tabs *{
	@include vendor-prefix(transform, translateZ(0));
}

.sticky-header-wrapper{
	position:relative;
}
.tabs {
	@extend .list-inline;

	li {
		display: block;
		float: left;

		a, span {
			display: block;
			height: 100%;
			padding-right: 24px;
			color: $tabs-headline-color;
			text-decoration: none;
			font-size: 18px;
			line-height: 20px;
			padding-bottom: 8px;
		}

		span {
			color: darken(white, 20%)
		}

		&.active {
			a {
				border-bottom: $tabs-border-width solid $tabs-bc;
			}
		}
	}

	$height: 40px;
	&.tabs-arrowed {
		height: $height;
		li {
			height: $height;
			a,span {
				position: relative;
				padding-right: 45px;
				line-height: $height;
				border-bottom: 1px solid white;


				i {
					@include sprite($icons-sort-desc);
					position: absolute;
					top: 16px;
					right: 25px;
				}
			}

			&:hover, &.active {
				a {
					border-bottom: $tabs-border-width solid $link-color;
				}
			}
		}
	}
	&.tabs-glossary {
		min-width: 100%;
		@extend .tabs-arrowed;
		li {
			a, span {
				padding-right: 20px;
				font-size: 24px;
			}
			span {
				color: #646d77;
			}
		}

	}
}

.tabs--title {
	li > a {
		font-size: 24px;
	}
}
.no-touch {
	.tabs li:hover {
		a {
			border-bottom: $tabs-border-width solid $tabs-bc;
		}
	}
}

.scrollable-tabs,
.scrollable-sticky-tabs,
.scrollable-tabs-autoscroll {
	$size: 33px;
	position: relative;
	overflow: hidden;
	height: 41px;

	.tabs-wrapper {
		position: relative;
		overflow: hidden;

		//IE and Safari FIX
		width: 99.8%;
		left: 1px;

		height: 100%;
		padding-top: 8px;
		.tabs {
			@include vendor-prefix(transition, all 0.5s linear);
			-webkit-transform: translateZ(0);
			-moz-transform: translateZ(0);
			-ms-transform: translateZ(0);
			-o-transform: translateZ(0);
			transform: translateZ(0);
			min-width: 100%;

			li {
				min-height: 33px;
			}
		}
	}

	$distance: 8px;
	$blur: 10px;
	$spread: -8px;

	.sticky-active {
		display: none;
		position: absolute;
		z-index: 300;
		height: 32px;
		top: 0;
		padding-top: 8px;
		background-color: $blue-gray;
		border-bottom: 1px solid $white;

		&.left {
			left: 31px;
			@include vendor-prefix(box-shadow, $distance 0px $blur $spread rgba(0, 0, 0, 1));
			a {
				margin-right: 16px;
			}
		}

		&.right {
			right: 31px;
			@include vendor-prefix(box-shadow, ($distance * -1) 0px $blur $spread rgba(0, 0, 0, 1));

			a {
				margin-left: 16px;
			}
		}


		a {
			height: 33px;
			color: $white;
			padding-right: 16px;
			padding-bottom: 7px;
			text-decoration: none;
			font-size: 18px;
			border-bottom: $tabs-border-width solid $link-color;
		}
	}

	.scroll-link {
		position: absolute;
		top: 2px;
		z-index: 200;
		width: $size;
		height: $size;
		background-color: $tabs-arrow-bgc;
		color: $text-primary;
		text-align: center;
		line-height: 33px;
		text-decoration: none;
		@include vendor-prefix(transition, all 0.5s ease-out);

		span {
			@extend .visuallyhidden;
		}

		.sticky-header-visible & {
			top: -6px;
		}

		&--prev {
			@include vendor-prefix(box-shadow, $distance 0px $blur $spread rgba(0, 0, 0, 1));
			@extend .icons-arrow-left;
			left: 0;

			&.scroll-link--hidden {
				left: -40px;
			}
		}

		&--next {
			@include vendor-prefix(box-shadow, ($distance * -1) 0px $blur $spread rgba(0, 0, 0, 1));
			@extend .icons-arrow-right;
			right: 0;

			&.scroll-link--hidden {
				right: -40px;
			}
		}

		&:before {
			margin-top: 8px;
			color: $tabs-arrow-color;
		}
	}

	&.scrollable-tabs-arrowed {
		height: 40px;
		.scroll-link {
			top: 6px;
		}
	}

	&.bottom-aligned {
		position: relative;
		bottom: -12px;

		@include respond-to("large") {
			bottom: 0;
		}
	}

	ul {
		position: absolute;
		left: 0;
	}
}

.sticky-header-wrapper.detached {
	.sticky-active {
		padding-top: 0;
		&.right {
			right: 52px;
		}
	}
}

@include ie8 {
	.scrollable-tabs,
	.scrollable-sticky-tabs {
		height: 35px;

		&.scrollable-tabs-arrowed {
			height: 46px;
		}
	}
}

.scrollable-tabs--hover {
	height: 40px;

	.tabs-wrapper {
		padding-top: 0;
	}

	.tabs > li {
		box-shadow: inset 0 -1px 0 white;
	}

	.scroll-link {
		/* line-height: 1.7; */
		top:2px;
		/* &:before,
		&:after {
			vertical-align: bottom;
		} */
	}
}
.scrollable-tabs--hover, .internal-concept {
	.tabs {
		li {
			a {
				border-bottom: $tabs-border-width solid transparent;
				-webkit-box-sizing: border-box;
				box-sizing: border-box;
			}

			&.active {
				a {
					border-bottom: $tabs-border-width solid $tabs-bc;
				}
			}

			@include respond-to("large") {
				&:hover {
					border-bottom: inherit !important;
				}
			}
		}
	}
}

.internal-concept {
	.tabs {
		li {
			a {
				color: #3d4855;
			}
		}
	}

	.scroll-link {
		background-color: $content-color;
		color: $text-primary;
	}
}

.tabs--underline {
	& > li {
		border-bottom: 1px solid #FFF;
	}
}

.scrollable-tabs-document-viewer {
	ul {
		height:40px;
		border: 1px solid #FFF;
		border-width:1px 0;
		width:100%;
		li {
			height:40px;
			a {
				line-height: 40px;
				position: relative;
				z-index: 1;
				font-size: 16px;
				@include respond-to("large") {
					&:hover {
						border-bottom-width:0
					}
				}
			}

		}
		li.mobile-active {
			a {
				border-bottom-width: $tabs-border-width;
			}
		}
	}
}
/* IE 7 - 8 */
@include ie8 {
	.scrollable-tabs-document-viewer {
		ul {
			height:33px;
			li {
				a {
					line-height: 30px;
					height:auto;
				}
			}
		}
	}
}
