.main-header {
	padding-top: 20px;

	.icon-svg {
		max-height: 40px;
		position: relative;
		left: -1px;
	}

	nav {
		padding: 0 !important;
	}

	.icons-slogan,
	.icons-darkbg-slogan {
		margin-top: 4px;
	}

	.icons-subtitle,
	.distinction-set {
		margin-top: 16px;
	}

	.icons-slogan,
	.icons-darkbg-slogan {
		@include respond-to("medium") {
			@include vendor-prefix(transform, scale(0.92));
			-webkit-transform-origin: 0;
			-moz-transform-origin: 0;
			-ms-transform-origin: 0;
			transform-origin: 0;
		}
	}

	.icons-subtitle,
	.distinction-set {
		@include respond-to("medium") {
			@include vendor-prefix(transform, scale(0.75));
			-webkit-transform-origin: 0;
			-moz-transform-origin: 0;
			-ms-transform-origin: 0;
			transform-origin: 0;
		}
	}

	&__logo {
		margin-top: 4px;
		.icons-logo {
			position: relative;
			@include respond-to("medium") {
				@include vendor-prefix(transform, scale(0.6));
				-webkit-transform-origin: 100% 0;
				-moz-transform-origin: 100% 0;
				-ms-transform-origin: 100% 0;
				transform-origin: 100% 0;
			}
		}
	}

	&__slogan svg {
		fill: $header-slogan;
	}

	&__division {
		color: $header-division-color;
	}

	&__group a {
		color: $header-group-nav-color;
	}

	.distinction-set {
		display: block;
		font: normal 15px $main-font;
		color: white;

		&:hover {
			text-decoration: none;
		}
	}


	@include respond-to("large") {
		.distinction-set.document-viewer  {
			text-align: left;
		}
	}

	@include respond-to("medium") {
		.row {
			.col-xl-10.col-md-8 {
				padding: 0 0 0 10px;
			}
			.col-xl-2.col-md-4 {
				padding: 0;

				.icons-logo {
					margin: 7px 12px 0 0;
				}
			}
		}
	}

	.pull-right.text-medium {
		color: white;
	}

	.lnk-language {
		margin-left: 20px;
		border-left: none !important;
		position: relative;
		display: inline-block;

		span {
			padding-left: 4px;
			padding-right: 8px;
		}

		i {
			color: $mid-gray-2;
		}

		& > a {
			line-height: 1.2;
			padding: 2px 2px 0 0;
    		display: block;
		}

		a:hover,
		a:focus {
			text-decoration: none !important;
		}

		a:hover {
			span {
				text-decoration: underline;
			}

			color: $header-language-navegation-hover;
		}

		.icons-globe-filled {
			font-size: 1.5em;
			vertical-align: middle;
		}

		@include respond-to("large") {
			display: none;
		}
	}

	.lnk-language__wrapper {
		position: absolute;
		left: 0;
		padding: 0;
		width: 100%;
		display: none;

		.lnk-language__options {
			list-style: none;
			display: block;
			border: none;
			padding: 0;
			a {
				padding: 0 0 0 30px;
				line-height: 1.8;

				&:hover {
					text-decoration: underline !important;
					color: $header-language-navegation-hover;
				}
			}
		}
	}

	.active.lnk-language {
		//background-color: black;
		.lnk-language__wrapper {
			display: block;
			//background-color: black;
		}
	}

	.col-xl-3 a {
		white-space: nowrap;
		text-decoration: none;
	}
}
