.icon-text-block {
	margin-bottom: 18px;
	float: left;
	width: 100%;
	.figure {
		img {
			max-width:100%
		}
	}
	.intro {
		color: #7c7d81;
		font-size: 18px;
		@include respond-to("medium") {
			font-size: 13px;
		}

		@include respond-to("small") {
			font-size: 18px;
		}
	}
	.main {
		display: block;
		color: #000;
		font-size: 35px;
		line-height: 1;
		margin: -5px 0 10px;
		@include respond-to("large") {
			font-size: 28px;
		}
		@include respond-to("medium") {
			font-size: 18px;
		}

		@include respond-to("small") {
			font-size: 35px;
		}
	}
}

.el-box {
	.contentdark-bg {
		.icon-text-block {
			.main {
				color:#FFF;
			}
		}	
	}
}
.el-box {
	.icon-text-block {
		.intro {
			color: #000;
		}
		.main {
			font-size: 24px;
    		margin: 0 0 6px;
			
		}
	}	
}
