.event-info {
	@extend .unstyled;

	li {
		display: inline-block;
		padding-right: 15px;

		.btn-orange {
			padding-top: 6px;
			padding-bottom: 6px;
		}
		.btn > i {
			font-size: 20px;
		}

		@include respond-to('small') {
			display: block;
			min-height: 40px;

			a {
				display: inline-block;
			}
		}
	}
}
