.label {
	@extend .nobreak;
	padding: 4px 7px;
	background-color: white;
	color: $base;
	line-height: 1;
	font-size: 11px;

	&.label-orange {
		background-color: $label-1-bgc;
		color: white;
	}
	&.label-grey {
		background-color: $label-2-bgc;
		color: white;
	}
	&.label-xl {
		font: bold 20px $basic-font;
		padding: 15px 20px;
	}
}

.row-title > .label {
	line-height: 1;
	display: inline-block;
} 

a.label-grey:hover {
	text-decoration: none;
	background-color: #999999;
}

// date label
.date-label {
	width: 100%;
	height: 62px;
	padding: 10px 5px 5px 5px;
	background: #8a8f93;
	color: white;
	font-family: $main-font;
	text-align: right;
	@include disable-select;

	@include respond-to("medium") {
		height: auto;
		padding: 0;
		background: transparent;
		color: $base;
		border-bottom: 1px solid $base;
		text-align: left;
	}

	span {
		display: block;
		font-size: 11px;
		line-height: 11px;
		margin-bottom: 2px;

		@include respond-to("medium") {
			display: inline;
		}

		&.day {
			font-size: 22px;
			line-height: 22px;
			margin-bottom: 2px;
		}
	}
}
// date label in Table
.noyear-label {
	span {
		font-size: 14px;
		line-height: 20px;
		&.day {
			font-size: 22px;
			line-height: 22px;
		}
	}

}


.text-tag {
	min-width: 47px;
	padding: 2px 6px;
	display: inline-block;
	font-size: 11px;
	line-height:18px;
	font-weight: bold;
	text-transform: uppercase;
	text-align: center;
}

.text-tag--dark {
	background-color: #98a2ad;
	color: #ffffff;
	/* &:hover {
		background-color: #B8C0CC;
	} */
}
.tags {
	@extend .clearfix;
	margin-bottom: 18px;
	.tag {
		@extend .text-tag;
		min-width: 41px;
		text-transform: none;
		font-weight: normal;
		float: left;
		color: #FFF;
		margin-right: 2px;

		/* &:hover {
			background-color: #B8C0CC;
			color: white;
		} */
	}
	.highlight {
		background-color: $link-color;
		&:hover {
			/* background-color: #eca01d; */
			text-decoration: none;
		}
	}
}
.tag.transparent-bg {
    border: 1px solid $darkbg-primary-text;
    padding: 1px 5px 1px;
    color: inherit;
    margin-right: 5px;
}
.table-6 {
	.tags {
		.tag {
			font-weight: bold;
		}

	}
}
@include ie8 {
	.date-label {
		position: relative;
	}
	.tag {
		min-width: 30px;
	}
}
