.content-header {
	@extend .clearfix;
	clear: both;

	@include respond-to("medium") {
		padding-left: 20px;
		padding-right: 20px;
	}

	padding: 12px 0 20px 0;

	> ul {
		float: left;
		@extend .clearfix;
	}

	li {
		display: inline-block;
		padding-left: 5px;
		vertical-align: middle;

		&:first-child {
			padding-left: 0;
			padding-right: 8px;
		}
	}

	a {
		color: white;
	}

	span.separator {
		color: $breadcrumb-separator-color;
	}

	.breadcrumb {
		li, li:first-child {
			padding-right: 8px;
			padding-left: 0;
		}

		li a {
			color: $breadcrumb-item-color;
		}

		li:last-child {
			span {
				color: $breadcrumb-item-last-color;
			}
		}
	}

	.icons-share-thin {
		color: $breadcrumb-options-share-icon-color;
		&:hover {
			color: $breadcrumb-options-share-icon-color-hover;
		}
	}


	a {
		padding-right: 8px;
	}

	.active {
		a {
			color: $breadcrumb-item-color;
		}
	}
	.actions {
		float: right;

		a:hover {
			text-decoration: none;
		}

		/* ul li {
			vertical-align: top;
		} */

		/* ul {
			margin-top: 5px;
		} */
	}
	@include respond-to("medium") {
		padding-top: 0;
		padding-bottom: 0;
		ul, nav {
			display: none;
		}
	}
	.icons-content-header-print {
		font-size: 16px;
		display: inline-block;
		&:before {
			vertical-align: middle;
		}
	}
}
