@import "sprites-mixin";

.icon-inline {
	display: inline-block;

	&.icon-middle {
		vertical-align: middle;
	}
}

.icons-download {
	font-size: 30px;
	line-height: 30px;
}

.icons-download-small {
	@extend .icons-download;
	font-size: 1em;
	line-height: 1;
	color: $link-color;
}

.icons-download-small-dark-bg {
	@extend .icons-download-small;
	color: white;
}

.icons-teaser-download-link {
	@extend .icons-download;
	font-size: 1.8em;
	color: white;
	width: 31px;
}

.icons-arrow-up-green, .icons-arrow-up-green-big {
	@extend .icons-arrow-up;
	line-height: 1;
	color: #406b31;
	font-size: 9px;
}

.icons-arrow-up-green {
	font-size: 0.5em;
}

.icons-arrow-down-red,
.icons-arrow-down-red-big {
	@extend .icons-arrow-down;
	line-height: 1;
	color: #e00034;
	font-size: 9px;
}

.icons-arrow-down-red {
	font-size: 0.5em;
}

.icons-mail-orange {
	@extend .icons-mail;
	color: $link-color;
	font-size: 12px;
}

.icons-phone-orange {
	@extend .icons-phone;
	color: $link-color;
}

.icons-content-header-share,
.icons-content-header-print {
	display: block;

	&:before {
		color: $breadcrumb-options-print-icon-color;
	}

	span {
		display: none;
	}

	&:hover:before {
		color: $link-color;
	}
}

.icons-content-header-share {
	@extend .icons-share;
	font-size: 15px;
}

.icons-content-header-print {
	@extend .icons-print;
}

.icons-quote-start,
.icons-quote-end {
	font-size: 0;
	color: $darkbg-primary-text;

	&:before {
		font-size: 26px;
	}
}

.icons-search {
	@extend .icons-loup;
}

.icons-close-badge,
.icons-apply-badge {
	display: inline-block;
	width: 33px;
	height: 33px;
	color: white;
	text-align: center;
	font-size: 14px;
	line-height: 34px;
}

.icons-close-badge {
	@extend .icons-close;
	background-color: #98a2ad;

	&:hover {
		background-color: $icons-close-badge-bgc;
	}
}

.icons-apply-badge {
	@extend .icons-success;
	background-color: $icons-apply-badge-bgc;
}
