// A tooltip thats appear on hover or touch events
//
// .ui-tooltip         - the default jQuery tooltip wrapper class
// .ui-tooltip-content - the container for the text
// .tooltip__arrow     - arrow at left-top side of container, to change the postion access tooltip.js

$tooltip-close-btn-width: 33px;
$tooltip-close-btn-height: 33px;
$tooltip-background: $darkbg-primary-text !default;

.ui-tooltip {
	padding: 0;
	border: none;
	background: none;
	box-shadow: none;

	&-content {
		position: relative;
		max-width: 350px;
		padding: 12px;
		z-index: 2;
		text-align: left;
		background: $tooltip-background;
		color: #fff;
		padding-right: $tooltip-close-btn-width;
	}

}

.tooltip {
	&__close {
		width: $tooltip-close-btn-width;
		height: $tooltip-close-btn-height;
		position: absolute;
		right: 0;
		top: 0;
		z-index: 2;
		text-align: center;
		line-height: $tooltip-close-btn-height;
	}

	&__arrow {
		$border-style: 10px solid $tooltip-background;
		position: absolute;
		top: -10px;

		// Triangle format
		border-left: 9px solid transparent;
		border-right: 9px solid transparent;
		border-bottom: $border-style;

		&.top {
			border-bottom: none;
			border-top: $border-style;
		}
	}
}

[data-tooltip] {
	white-space: nowrap;
}
