@mixin full-width-cols ($breakpoint) {
	@include respond-to($breakpoint) {
		display: block;
		width: auto;
		clear: both;
		float: left;
	}
}

table {
	width: 100%;
	padding: 0 !important;
	colgroup {
		col {
			float: none !important;
			min-width: 60px;
		}
	}
	td {
		vertical-align: top;
	}
}

.table-1 {
	th, td {
		padding: 18px 20px 18px 20px;
		line-height: 18px;

		@include respond-to("medium") {
			font-size: 11px;
			line-height: 14px;
			padding: 10px;
		}

		&:last-child {
			padding-left: 0;
		}
		&:first-child {
			padding-right: 0;
		}
	}

	.table__th {
		&.active {
			padding-right: 7px;
		}
		&.last {
			padding-right: 0;
		}
	}

	th {
		&:only-child {
			padding-left: 20px;
		}
	}

	// @note: this represents a date element
	strong {
		color: $table-1-date-color;
	}

	// Fake thead tag
	.thead {
		background-color: $table-1-header !important;
		td {
			color: #FFF;
		}
	}

	thead {
		tr {
			background-color: $table-1-header;
			color: white;
			th {
				font: normal 14px/14px $basic-font;
				text-align: left;

				&.text-center {
					text-align: center;
				}

				a {
					color: white;
					i {
						margin-left: 4px;
						visibility: hidden;
						display: inline-block;
					}
					&.active {
						i {
							visibility: visible;
							vertical-align: middle;
						}
					}
				}

			}
		}
		.aux-display {
			display: inline-block;
		}
	}

	tbody {
		tr {
			&:nth-child(odd) {
				background-color: $table-1-row-odd;
			}
			&:nth-child(even) {
				background-color: $table-1-row-even;
			}
			background-color: $content-color;
			td {
				a {
					.row-title,
					.row-description {
						display: block;
					}
					.row-title {
						font: normal 18px / 22px $main-font;
						color: $table-1-row-title;
						small {
							font: normal 13px / 18px $basic-font;
						}
					}
					&:hover {
						text-decoration: none;
						&.row-description {
							color: $link-color;
						}
					}
				}

				.row-category {
					font: normal 13px $basic-font;
				}

				.label {
					display: inline-block;
					min-width: 38px;
					text-align: center;
					padding: 4px 7px;
				}

				//text highlighted
				p.text-main {
					.label {
						font-size: inherit;
						padding: 2px;
						padding-top: 1px;
					}
				}
			}

			@include respond-to('small') {
				min-height: 168px;
			}
		}
	}

	.row-title {
		color: $text-color;
		// @todo remove: .text-highlighted has low priority than .table-1 .row-title
		&.text-highlighted {
			// @extend .text-highlighted;
		}
	}

	// a pragmatic solution!
	.table-gmb {
		margin-bottom: 0;

		tr {
			background: transparent !important;
		}

		th, td {
			color: white;
		}

		th {
			padding: 0;
		}

		td {
			padding: 10px 20px;
		}

		tbody {
			border-top: 1px solid white;

			td:first-child {
				padding-left: 0;
			}

			td:last-child {
				padding-right: 0;
			}
		}
	}

	// a, still more, pragmatic solution!
	.row-title .label {
		vertical-align: top;
	}
	.icons-tag {
		vertical-align: middle;
	}
	.icons-tag, .icons-tag ~ .label {
		margin-right: 5px;
	}
	.icons-tag:last-child {
		margin-right: 10px;
	}
	.list-categories {
		max-width: 50%;
		display: inline-block;

		li {
			display: inline-block;
		}

		.row-category {
			font-size: 11px;
			color: #787d82;
		}

		@include respond-to('medium') {
			max-width: 100%;
		}
	}

	.check-data {
		margin-bottom: 0;
		padding-left: 40px;

		.checkbox-input {
			left: 20px;
			top: 22px;

			@include respond-to('medium') {
				top: 2px;
				left: 10px;
			}
		}
	}

	// @todo: remove tables specificities
	.text-right {
		@extend .text-right;
	}
}

.check-data {
	padding-left: 40px;
	position: relative;
	margin-bottom: 20px;

	.checkbox-input {
		position: absolute;
		left: 0;
		top: 5px;
	}
}

.hover-bright {
	tr:hover, td:hover {
		color: #e29317;
		.row-title, .row-description, .row-pre-title {
			color: inherit !important;
		}
	}
}

.row-pre-title .icons-download-center {
	color: $link-color;
	padding: 0 5px;
	&:before {
		font-weight: bold;
	}
}
.row-pre-title {
	@include respond-to("medium") {
		strong {
			display: block;
		}
		.icons-download-center {
			padding-left: 0;
		}
	}
	@include respond-to("medium-and-up") {
		.has-separator {
			padding-right: 7px;
		    margin-right: 5px;
			border-right: 2px solid #4f5356;
		}
	}
}
.no-touch {
	tbody {
		td {
			a {
				&:hover {
					.row-pre-title,
					.row-title,
					.row-title small,
					.row-description {
						color: $link-color;
					}
				}
			}
		}
	}
}


/* table-togglable has a extra tr for the toggle
 */
$table-togglable-hr: $hr-dark !default;
.table-toggle {
 	img {
		display: block;
		margin-left: auto;
		margin-right: auto;
	}

	tbody {
		.table-toggle__row {
			cursor: pointer;
		}

		h3 {
			color: #FFF;
		}

		.odd {
			background-color: $pale-gray !important;
		}
		.even {
			background-color: #e6e9eb !important;
		}
		.table-toggle__row-child {
			background-color: $blue-gray !important;
		}
		.dark-bg p {
			color: $white;
		}

		&:nth-child(odd) {
			background-color: $pale-gray;
		}
		/* &:nth-child(odd) {
			background-color: $pale-gray;
		} */

		/* markup for hidden tr */
		/* &:nth-child(even) {
			background-color: $blue-gray;
		&:nth-child(even) {
			background-color: $table-togglable-internal-bgc;
			cursor: default;
		} */

		/* &:nth-child(4n+3) {
			background-color: #e6e9eb;
		} */
	}

	.hr {
		background-color: $table-togglable-hr;
	}
}


/* Variation without thead */
.table-1.table-no-head {
	tbody {
		tr {
			&:nth-child(odd) {
				background-color: #e6e9eb;
			}
			&:nth-child(even) {
				background-color: $pale-gray;
			}
		}
	}
}
.table-full-width {
	/* thead tr, */
	tbody tr td {
		@include full-width-cols(medium);
		white-space: normal;
	}


	@include respond-to("medium") {
		tbody {
			td {
				font-size: 14px;
				line-height: 22px;
				padding-top: 0;
				padding-bottom:5px;

				&:first-child {
					/* padding-left: 20px;
					padding-right: 20px; */
					padding-top:10px;
					padding-bottom:10px;

				}
				&:last-child {
					/* padding-left: 20px;
					padding-right: 20px; */
					padding-bottom:10px;
				}
			}
		}
	}
	@include respond-to("small") {
		tbody {
			td {
				&:first-child {
					padding-left: 10px;
					padding-right: 10px;

				}
				&:last-child {
					padding-left: 10px;
					padding-right: 10px;

				}
			}
		}
	}

	&.press-table {
		colgroup {
			@include full-width-cols(large);
		}
		tbody tr td {
			@include full-width-cols(large);
			@include respond-to("large") {
				padding-top: 0;
				padding-bottom:5px;

				&:first-child {
					padding-top:10px;
					padding-bottom:10px;

				}
				&:last-child {
					padding-bottom:10px;
				}
			}
		}
	}

	.icons-download {
		font-size: 1.3em;
    	vertical-align: middle;
	}
}

.table-full-width-insights {
	@include respond-to("medium") {
		thead {
			th {
				width:25%;
			}
		}
	}
}

@include ie8 {
	.table-1 .tr-odd, .press-table .tr-odd {
		background-color: $pale-gray;
	}
	.table-1 .tr-even, .press-table .tr-even {
		background-color: #e6e9eb;
	}
}

// table 2
.table-dark {
	th, td {
		padding: 10px 20px;

		@include respond-to("medium") {
			padding: 5px 14px;
		}
	}
	td, .row-description {
		color: #000;
		white-space: normal;
	}
	a:hover .row-description {
		color: inherit;
	}
	.text-primary {
		display: block;
	}
}

.thead-align-bottom thead > tr > th {
	vertical-align: bottom;
}

.thead-align-bottom .text-nowrap:hover > .aux-display {
	text-decoration: underline;
}

.cell-positive, .cell-negative {
	font-weight: bold;
	.icon-inline {
		vertical-align: top;
		margin-top: 4px;

		&.aux-v-align-middle {
			vertical-align: middle;
			margin-top: 0;
		}
	}
}

.cell-positive {
	color: #406b31;
}

.cell-negative {
	color: #e00034;
}

.table-wrapper {
	max-width: 100%;
}

.table-3 {
	tr {
		border-bottom: 1px solid #adb5bd;

		&.tr-highlight {
			background-color: #e6e9eb;
		}
	}

	th, td {
		padding: 10px 0;
	}

	&.table-3-x-padding {
		th, td {
			padding: 10px;
		}
	}

	th {
		font-weight: normal;
		padding-right: 10px;
		width:40%;
		&:first-child {
			width:60%
		}
	}

	td {
		font-weight: bold;
		width:40%;
		&:first-child {
			width:60%
		}
	}
}

.table-4 {
	border-collapse: separate;
	border-spacing: 10px;
	background-color: #e6e9eb;

	thead {
		th {
			width: 33%;
			color: #98a2ad;
			border-bottom: 1px solid #ced4d8;
			font-weight: normal;
			font-size: 13px;
			line-height: 18px;

			&.table-cell-empty {
				border-bottom: none;
			}
		}
	}

	tbody {
		td {
			padding-top: 0;
			font-size: 20px;
		}
	}

	&.smaller {
		border-spacing: 4px;
		background-color: transparent;

		thead {
			th {
				vertical-align: top;
				padding: 4px 10px;
			}
		}

		tbody {
			td {
				padding: 4px 10px;
				line-height: 18px;
				small {
					font-size: 13px;
				}
			}
		}
	}

}

.table-5 {
	color: #4f5356;
	font-size: 13px;

	th {
		padding: 0 10px 15px 0;

		@include respond-to("large") {
			padding-bottom: 10px;
		}
	}

	td {
		padding: 15px 10px;
	}

	tbody {
		$border: 1px solid #b9b9ba;
		border-bottom: $border;

		tr {
			border-top: $border;

			&:first-child, &.main-row, &~.main-row {
				border-top: none;
			}

			&.main-row {
				color: white;
				background-color: #98a2ad;
			}
		}
	}
}


.table-6 {
	.tags {
		margin-left: 10px;
	}
	tr {
		background-color:$pale-gray;
		a {
			color: $text-primary;
			font-size: 13px;
			line-height: 16px;
			&:hover {
				color: $link-color;
				text-decoration: none;
			}
		}
		div.download-link {
			color: lighten($text-primary, 20%);
			font-size: 13px;
			line-height: 16px;
			.icons-download-small {
				visibility: hidden;
			}
		}
		td, th {
			vertical-align: top;
			font-weight: normal;
			a, div {
				.icons-download-small {
					top:0;
					@extend .icons-download-small-grey
				}
			}
		}
	}

	.tr-odd {
	    background-color: #e6e9eb;
	}

	td.last{
		padding-left:40px;
		padding-top:37px;
		a {
			display:inline-block;
			color: $link-color;
			&:hover {
				text-decoration: underline;
			}
		}
	}
	tr.js-hide {
		height: 0px;
		overflow: hidden;
		/* visibility: hidden; */
		td {
			padding-top:0;
			padding-bottom:0;
			a, div, span {
				@include opacity(0);
			}

		}
	}
}

.table-6.table-full-width {
	@include full-width-cols(large);
	padding:0;
	tbody,
	tbody tr,
	tbody tr th {
		@include full-width-cols(large);
	}
	@include respond-to("large") {
		tr {
			> td:first-child {
				display: none;
			}
		}
		td {
			display: block;
			padding: 5px 20px;
			width:33%;
			float:left;
			clear:none;
		}
		td.last  {
			width:100%;
			float:left;
		}
		tr {
			float:left;

		}
		tr.first  {
				position:relative;
				padding-left:60px;
				padding-top:4px;
				display: block;
				th:first-child {
					position:absolute;
					left:0;
					top:0;
					width:auto;
					padding-right:0;
				}
			}

	}
	@include respond-to("medium") {
		td {
			width:50%;
		}


	}
	@include respond-to("small") {
		.js-toggler {
			display: none;
		}

		td {
			width:100%;

			&:last-child {
				padding-left: 20px;
			}
		}

	}
}

.newsletter-table-wrapper .table-full-width {
	thead tr {
		@include respond-to('medium') {
			display: block;
			width: 100%;
			float: left;
		}
	}
}


.pricing-details-values {
	min-width: 600px;
	col {
		&.active {
			background-color: #e6e9eb;
		}
	}
	tr {
		&:first-child {
			border-top: 1px solid #b9b9ba;
		}

		border-bottom: 1px solid #b9b9ba;
	}
	th, td {
		text-align: center;
		cursor: default;
	}
}

.table-content {
	table {
		.tags {
			margin-top: 5px;
			margin-bottom: 0;
		}
		tr {
			th, td {
				a.emphasize {
					font-size: 18px;
					line-height: 26px;
					color: $text-primary;
					@include respond-to("small") {
						font-size: 13px;
						line-height: 18px;

					}
				}
			}

		}
	}
}

.collapsible-wrapper {
	overflow-y: hidden;
}

.table-7 {
	tr {
		border-top: 1px solid #b9b9ba;

		&:first-child {
			border-top: none;
		}
	}

	th, td {
		padding: 6px 0;
	}

	th {
		color: white;
		font-weight: normal;
	}

	td {
		color: #d9dde0;
		text-align: right;

		&.cell-positive,
		&.cell-negative {
			padding-left: 6px;
		}

		&.cell-positive {
			color: #aaff00;
		}

		&.cell-negative {
			color: #e00034;
		}
	}
}


.press-table {
	border-spacing: 20px;

	td {
		padding: 20px;

		dl {
			margin: 0;
		}

		.icon-inline {
			@extend .aux-v-align-middle;
			margin-right: 10px;
			color: $link-color;
			font-size: 16px;
		}
	}

	tr {
		&:nth-child(odd) {
			background-color: #e6e9eb;
		}
		&:nth-child(even) {
			background-color: $pale-gray;
		}
	}

	dt {
		@extend .nobreak;

		a {
			color: $text-color;

			&:hover {
				color: $link-color;
				text-decoration: none;
			}

			&:focus {
				text-decoration: none;
			}
		}
	}

	@include respond-to('small') {
		td {
			padding: 10px;
		}
	}

}

.touch {
	.press-table dt a {
		&:hover {
			color: $text-color;
		}
	}
}

.table-bg-light {
	tbody {
		tr {
			&:nth-child(odd) {
				background-color: #FFF;
			}
			&:nth-child(even) {
				background-color: $pale-gray;
			}
		}
	}
}

.table-tooltip-wrapper {
	position: relative;
}

.table--tooltip {
	position: absolute;
	left: -13px;
	max-width: 350px;
	background: $darkbg-primary-text;
	padding: 12px;
	z-index: 2;
	margin-top: 10px;
	color: #fff;
	text-align: left;

	&-mark {
		position: absolute;
		top: -10px;
		left: 10px;
		width: 0;
		height: 0;
		border-left: 9px solid transparent;
		border-right: 9px solid transparent;
		border-bottom: 10px solid $darkbg-primary-text;
	}

	.lt-ie9 & {
		left: 7px;
	}
}

