.top-performer {
	position: relative;
	min-height: 317px;
	padding-left:$grid-padding;
	padding-right:$grid-padding;

	@include respond-to("large") {
		min-height: 1px;
		margin-bottom:55px;
		padding-bottom:6px;
	}
	@include respond-to("small") {
		padding-left:$grid-padding / 2;
		padding-right:$grid-padding / 2;
	}
	&:hover, &:focus {
		background-color: $contentdarkbg;
		position: relative;
		h4 {
			color: #000;
		}
	}
	.block-button {
		position: absolute;
		bottom: -55px;
		left: 0;
		width: 100%;
	}

}
@media (max-width: 1024px) {
	.top-performer {
	    min-height: 308px;
	}
}
.combi-head {
	padding-top: 18px;
	a {
		line-height: 29px;
	}
}

.top-performer--table {
	table-layout: fixed;
	a:hover, a:focus {
		text-decoration: none;
	}
	.top-performer--first {
		width: 50px;
	}
	.top-performer--last {
		width: 68px;
	}
	td, th {
		vertical-align: top;
		text-align: left;
		color: #FFF;
		font-weight: normal;
		padding: 4px 0 8px 0;
		font-size: 13px;
		line-height: 16px;
		* {
			line-height: 16px;
			font-size: 13px;
		}
		.tags {
			margin-top: 3px;
		}
	}
	td {
		border-top-width: 1px;
		border-top-style: solid;
	}

	tbody {
		h4 {
			font-weight: normal;
			color: #000;
		}
		p {
			color: #FFF;
			margin-bottom: 0;
		}
		tr:hover {
			background: #b8c0cc;
		}
	}
	tfoot {
		td {
			font-size: 11px;
			line-height: 13px;
			border: none;
		}
	}
}
