.homepage-top {
	position:relative;
}

.homepage-top--item {
	position:relative;
	background:#000;
	min-height: 180px;

	@include respond-to("small") {
		min-height: 260px;
	}
	.teaser-inner {
		position:absolute;
		left: $grid-padding * 2;
		right: $grid-padding * 2;
		bottom:40px;
		color:#FFF;
		z-index: 2;
		* {
			color:#FFF;
		}
		@include respond-to("small") {
			left: $grid-padding / 2;
			right: $grid-padding / 2;
		}
	}
	figure {
		image {
			max-width: 105%;
		}
	}
	.start-hl {
		margin-bottom: 12px;
		@include respond-to("small") {
			h1 {
				font-size:18px;
			}
		}
	}
	p {
		margin-bottom: 18px;
	}
	.homepage-top--list {

		li {
			float:left;
			margin-right:4px;
			a {
				@extend .button;
				min-width:61px;
				text-align: center;
			}
		}
	}


}

.controls-inside {
	.teaser-inner {
		bottom:60px;
		@include respond-to("small") {
			bottom:40px;
		}
	}
}

/* IE 7 - 9 */
@include ie9 {
	.homepage-top--list {

		li {
			a {
				&:hover, &:focus {
					text-decoration: none;
					background:url("../img/trans_ie.png") no-repeat 0 0;
				}
			}
		}
	}
}

.home-teaser-item {
	min-height: 374px;
	margin-left: 0;
	background: #3d4855;

	.teaser-inner {
		p, a {
			color: white;
		}
	}

	@include respond-to("large") {
		min-height: 0;
	}
}
