$susy: (
	flow: ltr,
	output: float,
	math: fluid,
	column-width: false,
	container: auto,
	container-position: center,
	last-flow: to,
	columns: 12,
	gutters: 0.05082,
	gutter-position: before,
	global-box-sizing: border-box
);

@include layout($susy border-box);

.row {
	@include container;
	clear: both;
}

a.row {
	display: block;
}

$total-cols: 12;

.grid12 {
	min-width: $min-width;
	max-width: $max-width;
}

// grid for non-responsive behavior
@for $i from 1 through $total-cols {
	.col-xl-#{$i} {
		min-height: 1px;
		@include span($i of $total-cols);
		@if $i == 12 {
			margin-left: 0 !important;
		}
	}
}

// grid for large screens
@for $i from 1 through $total-cols {
	@media (max-width: $break-large) {
		.col-lg-#{$i} {
			min-height: 1px;
			@include span($i of $total-cols);
			@if $i == 12 {
				margin-left: 0 !important;
			}
		}
	}
}

// grid for medium screens
@for $i from 1 through $total-cols {
	@media (max-width: $break-medium) {
		.col-md-#{$i} {
			min-height: 1px;
			@include span($i of $total-cols);
			@if $i == 12 {
				margin-left: 0 !important;
			}
		}
	}
}

// grid for small screens
@for $i from 1 through $total-cols {
	@media (max-width: $break-small) {
		.col-sm-#{$i} {
			min-height: 1px;
			@include span($i of $total-cols);
			@if $i == 12 {
				margin-left: 0 !important;
			}
		}
	}
}

// percentual grid
$total-percentage: 100%;
$fractions: '1-5', '1-4', '1-3', '1-2', '2-3', '3-4';

@each $fraction in $fractions {
	.col-#{$fraction}, .col-md-#{$fraction}, .col-lg-#{$fraction}, .col-sm-#{$fraction}, .col-xl-#{$fraction} {
		float: left;
		min-height: 1px;
	}
}

.col-1-5 {
	width: $total-percentage / 5;
}

.col-1-4 {
	width: $total-percentage / 4;
}

.col-1-3 {
	width: $total-percentage / 3;
}

.col-1-2 {
	width: $total-percentage / 2;
}

.col-2-3 {
	width: $total-percentage / 3 * 2;
}

.col-3-4 {
	width: $total-percentage / 4 * 3;
}

@include respond-to('medium') {
	.col-md-1-5 {
		width: $total-percentage / 5;
	}
}

// hide elements
@include respond-to("large") {
	.hide-lg {
		display: none;
	}
	.show-lg {
		display: block !important;
	}
}


@include respond-to("medium") {
	.hide-md {
		display: none;
	}
	.show-md {
		display: block !important;
	}
}


@include respond-to("small") {
	.hide-sm {
		display: none;
	}
	.show-sm {
		display: block !important;
	}
}

#main, // temporary passive only
.row [class*="col-"] {
	padding: 0 $grid-padding;
	@include border-box-sizing;
	&:first-child {
		margin-left: 0 !important;
	}
	@include respond-to("small") {
		padding: 0 $grid-padding / 2;
	}
}

.row.no-padding {
	> [class*="col-"] {
		padding: 0;
	}
}

@include ie8 {
	.row {
		white-space: normal;
	}
	#main,
	.center-block {
		width: $max-width;
	}
	.row:before,
	.row:after {
		content: "";
		display: table;
	}

	.row:after {
		clear: both;
	}
	.row.content {
		float: left;
		width: 100%;
	}
}
