.tab-content {
	display: table;
	.download-btn {
		padding: 25px 0;

		span {
			padding-left: 45px;
		}
		.icons-download {
			position: absolute;
			top: 50%;
			margin-top: -15px;
			font-size: 32px;
		}
	}

	.row:last-child {
		.download-btn {
			border: none;
		}
	}

	.tab-content__item:nth-child(odd) {
		padding-left: 0;
	}
	.tab-content__item:nth-child(even) {
		padding-right: 0;
	}

	@include respond-to("medium") {
		.tab-content__item {
			padding-left: 0;
			padding-right: 0;
		}
	}

	.row {
		.tab-content__item {
			overflow: hidden;
			position: relative;
			&:before {
				content:"";
				width: 100%;
			    position: absolute;
			    border: 0;
			    height: 1px;
			    background-color: #b9b9ba;
			    bottom:0;
			}
		}

		@include respond-to("medium-and-up") {
			display: table-row;

			.tab-content__item {
				display: table-cell;
				float:  none;
				&:before {
				    margin-left: -20px;
				}
				& + .tab-content__item:before {
					margin-right: -20px;
					margin-left: 0;
				}
			}
		}
	}

	.table-full-width td > .row:first-child {
		display: block;
		width: 100%;
	}
}


.tab-content__wrapper {
	border-top: 1px solid #b9b9ba;
	margin-top: -1px;
}
