// radio buttons will disappear if -webkit-appearance is applied to them
input:not([type=radio]):not([type=checkbox]) {
	-webkit-appearance: none !important;
	@include vendor-prefix(border-radius, 0);
}

.select-container {
	@extend .clearfix;
	margin-bottom: 20px;
	height: 32px;

	.chosen-select {
		width: 100%;
		font-size: 13px;
	}
}

.chosen-container-single {
	height: 100%;
	.chosen-single {
		height: 100%;
		background: none;
		background-color: white;
		border-radius: 0;
		box-shadow: none;
		border-color: #646d77;

		span {
			line-height: 32px;
		}
		div {
			b {
				@extend .icons-arrow-down;
				background: none;
				font-size: 13px;
				position: absolute;
				top: 6px;
				right: 5px;
			}
		}
	}

	&.chosen-container-active {
		.chosen-single {
			border-color: #646d77;
			span {
				color: $link-color;
			}
		}
	}

	.chosen-drop {
		border-radius: 0 !important;
		border-color: #646d77;
	}
}

@media only screen and (-webkit-min-device-pixel-ratio: 2),
	only screen and (min-resolution: 144dpi) {

	.chosen-container-single .chosen-single div b {
		background: none !important;
	}
}

.chosen-container {
	float: left;
	.chosen-results {
		li {
			&.highlighted {
				background: $darkbg-primary-text;
			}
		}
	}
}

.chosen-dark {
	.chosen-container-single {
		height: 32px;
		.chosen-single {
			height: 100%;
			background: none;
			background-color: #3d4855;
			border-radius: 0;
			border: none;
			color: white;
			span {
				line-height: 32px;
			}

			div {
				b {
					@extend .icons-arrow-down;
					font-size: 13px;
					background: none;
					position: absolute;
					top: 7px;
					right: 7px;
				}
			}

			&:hover {
				color: #e29317;
			}
		}
		&.chosen-with-drop {
			.chosen-single {
				box-shadow: none;
				div {
					b {
						background: none;
						@extend .icons-arrow-up;
					}
				}
			}
		}
	}
}

.chosen-solid {
	.chosen-container-single {
		height: 32px;
		.chosen-single {
			height: 100%;
			background: white;
			border-radius: 0;
			border: 1px solid $mid-gray;
			color: $text-color;
			span {
				line-height: 32px;
			}

			div {
				b {
					@extend .icons-arrow-down;
					font-size: 13px;
					background: none;
					color: $dark-gray;
					position: absolute;
					top: 7px;
					right: 7px;
				}
			}

			&:hover {
				color: $amber;
			}
		}
		&.chosen-with-drop {
			.chosen-single {
				box-shadow: none;
				div {
					b {
						background: none;
						/* @include sprite($icons-sort-asc);
						background-image: url($icons-sort-asc_image) !important;
						background-size: initial !important; */
						@extend .icons-arrow-up;
					}
				}
			}
		}
	}
}

.chosen-full-width {
	select {
		width: 100%;
		height: 32px;

	}

	&.no-search {
		.chosen-search {display:none;}
	}
}

.chosen-full-minus {
	select {
		width: 90%;
	}
}

.icons-globe {
	float: left;
	margin-left: 10px;
	margin-top: -2px;
}

