@include respond-to("large") {
	.form-search-wrapper {
		.form-search {
			display: none;
		}
	}
	.opened {
		.form-search {
			display: block;
			border: none;
			background: $main-menu-bg;

			@include respond-to("large-intermediate") {
				margin-top: 0;
			}

			input {
				width: 100%;
				height: 33px;
				max-width: 100%;
				padding: 0 43px 0 10px;
				line-height: 33px;
			}

			.btn-search {
				top: 8px;
				right: 3px;

				@include respond-to("large") {
					top: 10px;
					right: 10px;
				}
			}
		}

		.autocomplete-search-result {
			top: 49px;
			@include respond-to("medium") {
				top: 69px;
			}
		}
	}
}

@include respond-to("up-than-large") {
	.lt-ie10 .form-search-wrapper .form-search {
		display: block;
	}
}

.form-search {
	position: relative;
	right: 0;
	width: 100%;
	padding: 2px 0;
	margin: 5px 0;
	background-color: #FFF;
	border: 1px solid #a1a1a1;
	z-index: 2;
	@include vendor-prefix(transition, all 0.5s ease-in-out);

	.btn-search {
		position: absolute;
		top: -2px;
		right: -1px;
		padding: 0;
		width: 33px;
		height: 33px;
		text-align: center;

		i {
			color: $navegation-icon-search-color;
			font-size: 16px;
			line-height: 33px;
			&:before {
				line-height: 33px;
			}
		}
	}

	@include respond-to("large") {
		padding: 10px;
		margin: 0;
		.btn-search {
			position: absolute;
			top: 3px;
			right: 0px;
		}
	}

	@include respond-to("medium") {
		.btn-search {
			position: absolute;
			right: 0;
			top: 4px;
			padding: 0;
		}
	}

	.search-text {
		display: inline-block;
		border: none;
		outline: 0;
		width: 100%;
		padding-right: 40px;
		padding-left: 10px;
		line-height: 24px;
		height: 24px;
	}

	&.is-active {
		$plus: (100% / $total-cols) * 3;
		width: $plus + 100%;
		right: $plus;

		@include respond-to("large") {
			position: static;
		}

		@include respond-to("medium") {
			.btn-search {
				margin: auto;
				padding: 2px 10px;
			}
		}
	}

	::-webkit-input-placeholder {
	   color: #787d82;
	}

	:-moz-placeholder { /* Firefox 18- */
	   color: #787d82;
	}

	::-moz-placeholder {  /* Firefox 19+ */
	   color: #787d82;
	}

	:-ms-input-placeholder {
	   color: #787d82;
	}

	.search-text,
	.btn-search {
		&:focus {
			border: none;
		}
	}
}

.form-chart-filter {
	margin-bottom: 10px;

	@include respond-to("large") {
		margin-bottom: 20px;
		float: left !important;
	}

	label, select, input {
		font-size: 13px;
		line-height: 18px;
	}

	label {
		display: block;
		float: left;
		line-height: 32px;
		font-weight: bold;
	}

	select,
	.chosen-container {
		float: left;
		margin: 0 20px 0 10px;
	}

	select {
		min-width: 140px;
		height: 32px;
	}

	input {
		margin: 0 5px 0 10px;
	}
}

.product-finder-search {
	position: relative;

	input {
		width: 100%;
		height: 48px;
		border: 1px solid #cbd1d6;
		padding: 0 50px 0 15px;
	}

	a {
		position: absolute;
		top: 8px;
		right: 8px;
	}
}
/* IE 7 - 8 */
@include ie8 {
	.form-search .search-text {
		width:97px;
	}
	.form-search.is-active .search-text {
		width:185px;
	}
}
