.control-wrapper {
	min-height:4px;
	text-align:center;
}

.owl-controls {
	text-align: center;
	width: auto;
	margin: 0 auto;
	position:relative;
	display: inline-block;
	> div {
		display: inline
	}
	.icons-slider-next, .icons-slider-prev, .icons-slider-next-active, .icons-slider-prev-active {
		display: inline-block;
		position: absolute;
		top:5px;
		left:0;
	}
	.icons-slider-next, .icons-slider-next-active {
		left:auto;
		right:0;
	}
	.icons-slider-next:hover {
		//@include sprite($icons-slider-next-active);
		display: inline-block;
	}
	.icons-slider-prev:hover {
		//@include sprite($icons-slider-prev-active);
		display: inline-block;
	}

	.owl-pagination {
		margin:40px;
		.owl-page {
			span {
				background: $owl-bgc;
			    border-radius: 6px;
			    display: block;
			    height: 12px;
			    width: 12px;

				&:hover, &:focus {
					background: $owl-bgc-active;
				}
			}
		}
		.owl-page.active {
			span {
				background: $owl-bgc-active;
				&:hover, &:focus {
					background: $owl-bgc-active;
				}
			}
		}
	}
}

.is-slider--outer-arrow {
	@include respond-to('wide') {
		.owl-controls {
			position: static;

			.icons-slider-next, .icons-slider-prev, .icons-slider-next-active, .icons-slider-prev-active {
				background: none;
				top: 40%;
				left: -50px;
				text-indent: 0;
				color: $amber;
			}
			.icons-slider-next, .icons-slider-next-active {
				left:auto;
				right: -30px;
			}

			.icons-slider-next, .icons-slider-prev {
				font-size: 32px;
				color: $owl-outer-arrow-bgc;

				&:hover {
					background: none;
					text-indent: 0;
					color: $owl-outer-arrow-bgc-hover;
				}
			}

			.icons-slider-next {
				@extend .icons-arrow-right-big;
				&:before {
					font-weight: bold;
				}

			}

			.icons-slider-prev {
				@extend .icons-arrow-left-big;
				&:before {
					font-weight: bold;
				}
			}
		}
	}
}

/* .control-wrapper {
	margin-top: 5px;
}
 */
.owl-pagination {
	margin:40px;
	.owl-page {
		display: inline-block;
		zoom: 1;
		padding: 6px 4px;
		span {
			background: #b9b9bb;
		    border-radius: 6px;
		    display: block;
		    height: 12px;
		    width: 12px;

			&:hover, &:focus {
				background: $highlightedbg;
			}
		}
	}
	.owl-page.active {
		span {
			background: $highlightedbg;
			&:hover, &:focus {
				background: $highlightedbg;
			}
		}
	}
}

.is-slider--inner-arrow {
	@extend .is-slider--outer-arrow;
	@include respond-to('wide') {
		.owl-controls {
			.icons-slider-prev, .icons-slider-prev-active {
				left: 0;
				top: calc(50% - 30px + 16px);
			}

			.icons-slider-next, .icons-slider-next-active {
				right: 30px;
				top: calc(50% - 30px + 16px);
			}
		}
	}
}

.is-slider.t--size-6 {
	@include respond-to("large") {
		height: auto;
		margin-bottom: 0 !important;
	}
}
