.inlinesvg .icons-darkbg-slogan {
	background-image: none;
}

.icon-svg {
	display: block;
	fill: #FFF;
}

.grid12 {
	@include vendor-prefix(box-sizing, border-box);
	padding: 0 20px;

	@include respond-to('medium') {
		padding: 0 5px;
	}
}

.wrapper-full {
	width: 100%;
	background-color: $dark-bg;

	&--nav {
		background-color: $navegation-bg;
	}

	&--light {
		background-color: $blue-gray;
	}
}
