.form {
	&--horizontal {
		.form__label {
			padding-top: 5px;
		}
	}

	&__group {
		margin-bottom: 15px;
		@extend .clearfix;

		&--md {
			display: inline;

			@include respond-to('medium') {
				margin-bottom: 0;
				margin-top: 15px;
				display: block;
			}

			.form__label {
				margin-left: 0.40465% !important;
			}
		}

		&.is-invalid {
			.form__label {
				color: $negative;
			}

			.form__text {
				border: 1px solid $negative;
			}

			.form__feedback:after {
				@include icons-close;
				color: $negative;
			}

			.chosen-single {
				border-color: $negative !important;
			}

			.form__option, .form__option a {
				color: $negative;
			}
		}

		&.is-valid {
			.form__feedback {
				&:after {
					@include icons-success;
					color: $positive;
				}

				.form__text {
					border: 1px solid $positive;
				}
			}
		}

		&--fake {
			display: inline;
			margin: 0;
			padding: 0;

			&:before, &:after {
				display: none;
			}
		}
	}

	&__text {
		display: block;
		width: 100%;
		height: 32px;
		padding: 6px 12px;
		border: 1px solid $mid-gray;
	}

	&__option {
		display: inline-block;
		margin-right: 18px;

		input {
			vertical-align: middle;
		}

		&--block {
			display: block;
		}
	}

	&__error-message {
		display: none;
		color: $negative;

		&.is-visible {
			display: block;
		}
	}

	&__feedback {
		position: relative;

		&:after {
			position: absolute;
			right: -20px;
			top: 8px;
		}
	}

	&__text-area, &__text {
		color: $base;
		&:focus {
			outline: none;
		}
	}

	&__text-area {
		resize: none;
		width: 100%;
	}
}


.unsubscribe-form {
	position: absolute;
	background-color: $lk-gray;
	color: white;

	&__wrapper {
		z-index: 300;
		position: relative;
		height: 1px;
	}
}
