@font-face {
	font-family: 'UniversDeutscheBank';
	font-style: normal;
	src: url("../fonts/universdeutschebank-regular.eot");
	src: url("../fonts/universdeutschebank-regular.eot?#iefix") format("embedded-opentype"), url("../fonts/universdeutschebank-regular.woff") format("woff"), url("../fonts/universdeutschebank-regular.ttf") format("truetype"), url("../fonts/universdeutschebank-regular.svg#UniversDeutscheBankRegular") format("svg");
}


@font-face {
	font-family: 'UniversDeutscheBank';
	font-style: italic;
	src: url("../fonts/universdeutschebank-italic.eot");
	src: url("../fonts/universdeutschebank-italic.eot?#iefix") format("embedded-opentype"), url("../fonts/universdeutschebank-italic.woff") format("woff"), url("../fonts/universdeutschebank-italic.ttf") format("truetype"), url("../fonts/universdeutschebank-italic.svg#UniversDeutscheBankItalic") format("svg");
}


p {
	margin: 0 0 8px 0;
}

.main-font {
	font-family: $main-font !important;
}

.basic-font {
	font-family: $basic-font !important;
}

.page-hl {
	display: block;
	padding: 15px 0 15px;
	@include respond-to("medium") {
		padding-left: $grid-padding;
	}
	@include respond-to("small") {
		padding-left: 0;
	}

	&.has-border {
		border-top: 2px solid #FFF;
		@include respond-to("medium") {
			border-top-width: 0;
		}
	}

	h1, h2, h3 {
		padding-left: 0;
		padding-right: 0;
	}
}


h1, .h1 {
	margin-bottom: 0;
	@include respond-to("medium") {
		padding-left:  $grid-padding;
		padding-right:  $grid-padding;
	}
	@include respond-to("small") {
		padding-left:  0;
		padding-right: 0;
	}
}

h1, .h1, h2, .h2, h3, .h3 {
	font-family: $main-font;
	font-weight: normal;
	line-height: 1.2;
	&:first-child {
		margin-top: 0;
	}
	small, .small {
		font-size: 0.6em;
		color: $text-color;
	}
}

h1, .h1 {
	font-size: 36px;
	line-height: 40px;
	color: $title-h1-color;

	@include respond-to("medium") {
		font-size: 32px;
		line-height: 36px;
	}

	@include respond-to("small") {
		font-size: 28px;
		line-height: 32px;
	}
}

h2, .h2 {
	font-size: 24px;
	line-height: 26px;
	margin: 0 0 6px 0;
	color: $title-h2-color;

	@include respond-to("medium") {
		font-size: 22px;
		line-height: 24px;
	}

	@include respond-to("small") {
		font-size: 20px;
		line-height: 22px;
	}

	.label {
		font-size: 1em;
	}
}

h3, .h3 {
	font-size: 18px;
	line-height: 22px;
	margin: 0;
	color: $title-h3-color;
}

h4, .h4 {
	font: normal 14px / 20px $main-font;
	color: $base;
	margin-bottom: 0;
	&:first-child {
		margin-top: 0;
	}
}
h4.has-margin, .h4.has-margin {
	margin-bottom: 12px;
	margin-top: 12px;
}

h5, .h5 {
	font-size: 14px;
	font-weight: normal;
}

h6, .h6 {
	font-size: 14px;
	font-weight: normal;
	margin-bottom: 10px;
}

.hl-xxl {
	font-size: 80px;
	font-family: $main-font;
	line-height: 1;
	color: white;
}

.teaser-header {
	font-size: 18px;
	border-bottom: 1px solid $title-header-bc;
	display: inline-block;
	padding-right: 20px;
	padding-bottom: 4px;

	h2 {
		display: inline;
	}

	h3, h4 {
		font-weight: normal !important;
	}

	.h3 {
		font-size: 18px;
		color: white;
	}

	small {
		font-size: 72%;
		color: $darkbg-primary-text;
	}

	&--with-share {
		width: 100%;
		padding-right: 0;
		@extend .clearfix;
		position:relative;
		border-bottom: 0;

		.social-bar__wrapper {
			right: 0;
			position: absolute;
		}
	}

	&--with-share .h2 {
		float: left;
		border-bottom: 1px solid $title-header-bc;
	}
	
	&--no-border {
		border-bottom: 0;
		.h2 { 
			border-bottom: 0;
		}
	}
	
	.social-bar__wrapper {
		float: right;
	}
}

.content-hl {
	h3 {
		font-family: $main-font;
		font-size: 18px;
		font-weight: normal;
		line-height: 22px;
		color: #3d4855;
		margin-bottom: 23px;

		@include respond-to("medium") {
			margin-bottom: 12px;
		}

		small {
			font-size: 13px;
			color: #3d4855;
		}
	}
}

.dark-bg {
	p, h1, h2, h3, h4, h5, h6,
	.h1, .h2, .h3, .h4, .h5, .h6,
	li, label {
		color: $dark-bg-elements-color
	}

	a {
		color: $white;
	}

	.text-primary {
		color: $darkbg-primary-text;
	}

	.text-highlighted {
		color: $darkbg-highlighted-text;
	}

	.text-brighten {
		color: $link-color;
	}

	.text-contrast {
		color: #3d4855;
	}

	.chosen-container {
		li {
			color: $text-color;
		}
	}

	i[class*="icons-quote-start"] {
		color: white;
	}

	i[class*="icons-quote-end"] {
		color: white;
	}

	// since ".dark-bg a" has more specificity than ".main-header__division"
	// the nexts lines are needed
	// tag: @css-specificity
	.main-header__division {
		color: $header-division-color;
	}
}

.contentdark-bg {
	background: $contentdarkbg;
	/* p, a, h1, h2, h3, h4, h5, h6, li,*/
	.teaser-main {
		color: black;
		a.block-link {
			color: black;
		}
		.h2, .h3 {
			color: white;
			a.block-link {
				color: white;
			}
		}
	}
	.hr {
		background: lighten($contentdarkbg, 10%);
	}
	td, th {
		border-color: lighten($contentdarkbg, 10%);
	}
	&:hover, &:focus {
		background: #b8c0cc;
		h1, h2, h3, h4, h5, h6,
		.h1, .h2, .h3, .h4, .h5, .h6 {
			color: #FFF;
			a {
				color: inherit;
			}
		}
		.date.contentdark-bg {
			background: #98a2ad;
			border-color: #98a2ad;
		}
		.icons-arrow-link {
			background-color: $highlightedbg;
		}
		.download-teaser-link {
			color: white;
		}
		.icons-teaser-download-link {
			@extend .icon-download;
		}
		.hr {
			background: lighten($contentdarkbg, 20%);
		}
	}
	.download-teaser-link {
		color: white;
		.download-data {
			color: $text-color;
		}
		&:hover, &:focus {
			text-decoration: none;
			.download-data {
				color: $text-color;
			}
			.icons-teaser-download-link {
				@extend .icon-download;
			}
		}
	}

	p {
		color: black;
	}
}

.download-teaser-link {
	.download-data {
		color: $text-color;
	}
}

.highlighted-bg {
	background: $highlightedbg;

	p, a, h1, h2, h3, h4, h5, h6, li,
	.h1, .h2, .h3, .h4, .h5, .h6 {
		color: white;
	}
}

.contentlight-bg {
	background: $content-color;
	.icons-teaser-download-link {
		@extend .icons-download;
	}
	&:hover, &:focus {
		background: #FFF;
		h1, h2, h3, h4, h5, h6,
		.h1, .h2, .h3, .h4, .h5, .h6 {
			color: $link-color;
			a {
				color: inherit;
			}
		}
		.date.contentdark-bg {
			background: #98a2ad;
			border-color: #98a2ad;
		}
		.icons-arrow-link {
			background-color: $highlightedbg;
		}
		.download-teaser-link {
			color: $highlightedbg;
			.download-data {
				color: $highlightedbg;
			}
		}
	}
}

.h-subline {
	border-bottom: 1px solid white;
	line-height: 1.5;
	&.h-subline-orange {
		border-color: $link-color;
	}
	&.h-subline-grey {
		border-color: $darkbg-primary-text;
	}
}

.h-subline {
	border-bottom: 1px solid white;
	line-height: 1.5;
	&.h-subline-orange {
		border-color: $link-color;
	}
	&.h-subline-grey {
		border-color: $darkbg-primary-text;
	}
}

// inline styles

mark {
	color: white;
	background-color: $link-color;
	padding: 0 3px;
}

.small {
	font-size: 0.75em !important;
}

.regular {
	font-size: 1em!important;
}

.medium {
	font-size: 1.10em !important;
}

.big {
	font-size: 1.25em !important;
}

.emphasize {
	font-size: 18px !important;
	line-height:26px;
}

.text-medium {
	font-size: 11px;
	line-height: 14px;

	.heading {
		font-size: 11px;
		font-weight: bold;
		font-family: $basic-font;
		margin-bottom: 5px;
	}
}
.lk-small {
	font-size: 13px;
}
// alignment classes

.text-left {
	text-align: left;
}

.text-center {
	text-align: center;
}

.text-right {
	text-align: right;
}

.text-justify {
	text-align: justify;
}

.text-nowrap, .nobreak {
	white-space: nowrap;
}

// transform classes

.text-lowercase {
	text-transform: lowercase;
}

.text-uppercase {
	text-transform: uppercase;
}

.text-capitalize {
	text-transform: capitalize;
}

.text-normal {
	font-weight: normal;
}

// text colors

.text-main {
	color: $text-color;
}
.text-primary {
	color: $text-primary;
}
.text-highlighted {
	color: $link-color;
}
.text-base {
	color: $base;
}
.text-light {
	color: $darkbg-primary-text;
}

// lists

.unstyled {
	list-style: none;
	margin: 0;
	padding: 0;
}

.list-inline {
	@extend .unstyled;
	li {
		display: inline;
	}
	&.pipe-separator {
		li {
			padding: 0 5px 0 7px;
			border-left: 1px solid;
			&:first-child {
				padding-left: 0;
				border-left: none;
			}

			/* &:last-child {
				border-left: 0;
			} */
		}
	}

	&.comma-separator {
		li:after {
			content: ", ";
		}
		li:last-child:after {
			content:"";
		}
	}
}

// blockquotes
blockquote {
	margin: 0;
	padding: 0;
}

.blockquote {
	margin: 0;
	padding: 0;
	font-family: $main-font;
	font-size: 24px;
	line-height: 1;
	position: relative;
	@include respond-to("medium") {
		font-size: 22px;
	}

	i[class*="icons-quote"],
	i[class*="icons-quote"] {
		display: inline-block;
		position: relative;
	}
	i[class*="icons-quote-start"] {
		margin: 0 5px 0 0;
		top: 1px;
	}
	i[class*="icons-quote-end"] {
		margin: 0 0 0 5px;
		bottom: -6px;
	}
	figure {
		margin-bottom: 0;
		@include respond-to("large") {
			display: none;
		}
	}
	.author {
		color: $teaser-quote-author;
		font-size: 18px;
		@include respond-to("medium") {
			font-size: 16px;
		}
	}
}

.internal-concept .blockquote .author {
	color: $text-color;
}

.blockquote.dark-bg {
	background: $contentdarkbg;
	/* Old browsers */
	background: -moz-linear-gradient(left, $contentdarkbg 0%, $contentdarkbg 51%, #1e2227 100%);
	/* FF3.6+ */
	background: -webkit-gradient(linear, left top, right top, color-stop(0%, $contentdarkbg), color-stop(51%, $contentdarkbg), color-stop(100%, #1e2227));
	/* Chrome,Safari4+ */
	background: -webkit-linear-gradient(left, $contentdarkbg 0%, $contentdarkbg 51%, #1e2227 100%);
	/* Chrome10+,Safari5.1+ */
	background: -o-linear-gradient(left, $contentdarkbg 0%, $contentdarkbg 51%, #1e2227 100%);
	/* Opera 11.10+ */
	background: -ms-linear-gradient(left, $contentdarkbg 0%, $contentdarkbg 51%, #1e2227 100%);
	/* IE10+ */
	background: linear-gradient(to right, $contentdarkbg 0%, $contentdarkbg 51%, #1e2227 100%);
	/* W3C */
	@include respond-to("large") {
		background: $contentdarkbg;
	}
}

.footnote {
	@extend .text-medium;
	white-space: normal;
	p {
		margin: 0 0 4px 0;
	}

	sup {
		font-size: 8px;
	}
	&.light-bg {
		p {
			color: $dark-gray;
		}
	}
}

.footnote.has-sup p {
	padding-left: 8px;
	position: relative;
	margin-bottom: 0px;
	line-height: 15px;
	sup {
		display: inline-block;
		position: relative;
		top: -5px;
		margin-right: 5px;
	}
}

.scrollable-tabs.teaser-tab {
	height: 40px;
	.tabs li {
		height: auto;
		a.h2 {
			padding-bottom: 5px;
			height: 40px;
			border-bottom: 1px solid #FFF;
			font-size: 24px;
			color:#98a2ac
		}
	}
	.tabs li.active {
		a.h2 {
			border-bottom: 5px solid $highlightedbg;
			color:#FFF;
		}
	}
}
/* IE 7 - 9 */
@include ie9 {
	.blockquote.dark-bg {
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#98a2ad', endColorstr='#1e2227',GradientType=1 );
		/* IE6-9 */
	}
}
/* IE 7 - 8 */
@include ie8 {
	.scrollable-tabs.teaser-tab {
		.tabs li {
			a.h2 {
				height: 29px;
			}
		}
		.tabs li.active {
			a.h2 {
				height: 25px;
			}
		}
	}
}

.product-link {
	color: $teasers-product-link-color;
}

.lk-icon:hover,
.lk-icon:active,
.lk-icon:focus {
	text-decoration: none !important;
	border: none;
	span {
		color: inherit;
	}
}

.lk-icon:hover,
.h3.lk-icon:hover {
	color: $link-color;
	span {
		color: inherit;
	}
}

/* .lk-icon .icons-arrow-right:before {
	font-weight: bold;
} */

.lk-small {
	font-size: 11px;
	&:hover {
		color: $link-small-color;
		text-decoration: none;
	}
}

.lk-disable {
	span {
		visibility: hidden;
	}

	color: #adb0b3;
}
