#main {
	max-width: $max-width;
	min-width: $min-width;
	@include respond-to("medium") {
		padding: 5px;
	}
}

.js-toggle-box {
	@extend .hide;
}

.content-wrap {
	@include respond-to("medium") {
		padding: 0 20px;
	}

	@include respond-to("small") {
		padding: 0;
	}
}

.internal-concept {
	background-color: $content-color;
	padding-top: 18px;
	padding-bottom: 6px;
	position: relative;

	h1, .h1,
	h2, .h2,
	h3, .h3,
	h4, .h4,
	h5, .h5,
	h6,	.h6 {
		color: $text-color;
		margin-bottom: 0.5em;
	}

	h1,.h1 {
		@include respond-to("medium") {
			padding-left: 0;
		}
	}

	.text-highlighted {
		color: $link-color;
	}

	p {
		clear: both;

		&.h3.text-base {
			color: $base;
		}
	}

	.h3 > i {
		font-size: 80%;
	}

	.hr {
		background-color: #cfd1d4;
	}

	.footnote {
		position: relative;
		margin: 5px 0 20px;
	}

	.el-box {
		margin-bottom: 18px;
	}

	@include respond-to("small") {
		padding-top: 10px;
	}

	&.dark-bg {
		background-color: $contentdarkbg;

		p, a, h1, h2, h3, h4, h5, h6,
		.h1, .h2, .h3, .h4, .h5, .h6,
		li, label {
			color: white;
		}

		.text-base {
			color: $base;
		}
	}

	.teaser-quote .teaser__body {
		padding-left: 0;
		padding-right: 0;
	}
}

.internal-concept--box {
	padding-top: 30px;
	padding-bottom: 30px;
}

.internal-concept--facts {
	.h1 {
		font-size: 35px;
		font-family: $basic-font;
		.text-base {
			color: #3d4853;
		}
		sup {
			font-size: 60%;
			top: -.75em;
		}
	}
	.h4 {
		display: block;
		line-height: 1.2;
		margin-top: 10px;
	}
	.h4, .h3 {
		color: $text-primary;
	}
	.h3 {
		font-weight: bold;
	}
	.highlight-icon {
		min-height: 125px;
	}
	.hr {
		margin-right: 20px;
		margin-bottom: 35px;
		margin-top: 25px;
	}

	@include respond-to("large") {
		.hr {
			display: none;
		}
	}
}

[class*="col-"].internal-concept {
	padding-top: 18px;
	padding-bottom: 6px;
}

.internal-bordered-container {
	background-color: $content-color;
	border-top: 1px solid #d8dde1;
}

.content-teaser:hover {
	/* h2, h3 {
		color: $link-color;
		a {
			color: inherit;
		}
	} */
	.icons-arrow-link {
		background-color: $highlightedbg;
	}
	background-color: #FFF;
}

.pic-left {
	.text-box {
		padding-left: 0;
		padding-right: 40px;
		margin-bottom: 18px;
		@include respond-to("small") {
			padding-left: 10px;
			padding-right: 10px;
		}
	}
	.text-box.col-lg-12 {
		@include respond-to("large") {
			padding-left: 20px;
			padding-right: 20px;
		}
		@include respond-to("small") {
			padding-left: 10px;
			padding-right: 10px;
		}
	}
}

.pic-right {
	.el-box {
		float: right;
	}
}

.pic-bottom-right {
	@include respond-to("medium-and-up") {
		min-height: 260px;
	}
	.el-box {
		figure {
			position: absolute;
			bottom: 0;
			right: 0;
		}
	}
}

.pic-full {
	.text-box {
		padding-top: 20px !important;
		padding-left: 20px !important;
		padding-right: 20px !important;

		p { font-size: 14px; line-height: 1.4; }
	}
}

.subline {
	background-color: $contentdarkbg;
	padding-top: 18px;
	padding-bottom: 6px;
		.contentdark-bg:hover {
			background-color: $contentdarkbg;
		}
	@include respond-to("xl") {
		padding-top:8px;
	}
}

.news-top {
	.news-title {
		position: relative;
		min-height: 62px;
		border-bottom: 1px solid $hr-content;
		[class*="col-"] {
			min-height: 62px;
			padding: 0;
			h2 {
				position: absolute;
				bottom: 0;
				@include respond-to("large") {
					position: initial;
					bottom: auto;
				}
			}
			.download-btn {
				margin-top: 10px;
			}
		}
	}
}

.table-actions {
	a:first-child {
		margin-left: 35px;
	}
}

.related-documents {
	.h6 {
		margin: 5px 0;
		font-weight: bold;
		border-bottom: 1px solid #b9b9ba;

		&:first-child {
			margin-top: 0;
		}
	}

	.unstyled {
		li {
			a {
				display: block;
				padding: 5px 0;
				position: relative;
				padding-left:24px;
				i[class*="icon"] {
					position: absolute;
					top:8px;
					left:0;
				}

				i[class*="-link"] {
					margin-left: 0;
					left: 2px;
					top: 12px;
				}

				.tag  {
					display: inline-block;
					font-size: 11px;
					line-height: 18px;
					margin-right: 5px !important;
				}
			}
		}
	}

	@include respond-to("medium") {
		.no-padding-right {
			padding-left: 0;
		}
	}
}
.block-link.js-video {
	position: relative;
	.icons-video {
		position: absolute;
		top:50%;
		left:50%;
		margin:-13px 0  0 -20px;

	}
}
.scroll-content-h {
	width:100%;
	@extend .clearfix;
	overflow-x: auto;
	@include respond-to('large') {
		width:auto;
	}
}
/* IE 7 - 8 */
@include ie8 {
	.scroll-content-h {
		overflow-x: visible;
	}
}

.teaser-hl {
	min-height: 179px;

	@include respond-to("large") {
		min-height: 137px;
	}

	@include respond-to("medium") {
		min-height: 0;
	}

	header {
		position: absolute;
		bottom: 20px;
		left: 20px;
		padding: 20px !important;
		background: rgb(152, 162, 173);
		background: rgba(152, 162, 173, 0.65);


		@include respond-to("medium") {
			position: static;
			background: rgba(152, 162, 173, 1);

			.h3 {
				padding: 0 20px;
			}
		}

		@include respond-to("small") {
			.h3 {
				padding: 0;
			}
		}
	}
}

.lt-ie9 .teaser-hl header {
	-ms-filter:"progid:DXImageTransform.Microsoft.Alpha(Opacity=65)";
}

.highlight-icon {
	position: relative;

	&__icon {
		position: absolute;
		top: 0;
		left: 0;
	}

	&__content {
		width: 100%;
		padding-left: 100px;

		.h3, .h4 {
			color: #858585;
		}

		@include respond-to("medium") {
			padding-bottom: 25px;
		}

		@include respond-to("large") {
			.h1 {
				padding: 0;
			}
		}
	}

	.icons-4x + .highlight-icon__content {
		padding-left: 80px;
	}

	.h1, .h2, .h3, .h4 {
		display: inline-block;
		margin-bottom: 5px;
	}

	.aux-pos-bt {
		bottom: -20px;
	}

	@include respond-to("large") {
		&.highlight-icon--inline {
			.highlight-icon__icon {
				i {
					font-size: 4em;
				}
			}
		}

		.aux-pos-bt {
			bottom: 5px;
		}
	}

	figure {
		float: left;
		max-width: 78px;
	}
}

figure {
	position: relative;
	.btn-icon-only {
		position: absolute;
		bottom: 10px;
		right: 10px;
	}
}

.toggle-tags-btn {
	font-size: 11px;
	text-decoration: none;
	display: block;
	&:hover, &:focus, &:active {
		text-decoration: none;
		border: none;
	}
}

.internal-header {
	line-height: 1.7;
	.internal-header__date,
	.tags-wrapper,
	.toggle-tags,
	.js-expand {
		display: inline-block;
	}

	// .toggle-tags {
	// 	max-width: 75%;
	// }

	.tags-wrapper {
		max-width: 85%;
		@include respond-to("xlarge") {
			width: 80%;
		}
	}
	.internal-header__date {
		vertical-align: top;
		font-size: 13px;
		line-height: 1.5;
		margin-right: 10px;
		margin-top: 5px;
	}

	.col-xl-4.col-lg-12 {
		@include respond-to("large") {
			padding-left: 0;
		}
	}
}

.no-flexbox .internal-header,
.no-flexwrap .internal-header {
	line-height: 2;
}

// visually hide preloader only when js is enabled
.js {
	.preload-teaser {
		@extend .visuallyhidden;
	}
}

.worldmap-bg {
	background-image: url(../img/worldmap-bg.png);
	background-repeat: no-repeat;
	background-position: top right;
}

.download-center-actions {
	@include respond-to("small") {
		display: none;
	}
	a {
		font-size: 16px;
		margin-left: 20px;
		line-height: 30px;

		&.active,
		&:hover {
			color: $link-color;
		}

		&:first-child {
			margin-left: 0;
		}
		i {
			vertical-align: middle;
		}
	}
}

.internal-concept__sidebar {
	@include respond-to("medium-and-up") {
		padding: 0 !important;
	}
}

.content-video {
	.content-video__media {
		position: relative;
		img {
			width: 100%;
			height: auto;
		}

		.decorator {
			margin: 10px;
		}
	}
	.content-video__body {
		position: relative;
		@include respond-to("large") {
			padding-left: 0;
			padding-right: 0;
			padding-top: 20px;
		}

		.aux-pos-bt{
			margin-left: 20px;
		}
	}
}
