.flash-message {
	display: inline-block;
	line-height: 30px;
	vertical-align: middle;
	color: white;
	margin-left: 10px;

	&__wrapper {
		position: relative;
		background-color: $amber;
	}

	&__close {
		position: absolute;
		right: 10px;
		top: 6px;
		color: lighten($amber, 40%);
		font-size: 10px;
	}
}
