.linear-nav {
	margin-bottom: 4px;
	
	span {
		white-space: nowrap;
	    text-overflow: ellipsis;
	    overflow: hidden;
	    width: 90%;
	    display: block;
	}

	&:nth-child(3n+1) {
		margin-left: 0;
	}

	@include respond-to("large") {
		&:nth-child(3n+1) {
			margin-left: gutter();
		}

		&:nth-child(odd) {
			margin-left: 0;
		}
	}
}

.btn-arrowed--nav {
	background-color: transparent;
	border-top: 1px solid $lk-gray;
	border-bottom: 1px solid $lk-gray;

	&:hover {
		background-color: $lk-gray;
	}

	i {
		position: absolute;
		right: 0;
		top: 0;
		margin-top: -1px;
	}
}