.autocomplete-search-result {
	position: absolute;
	top: 0;
	right: 0;
	width: 100%;
	background: $main-menu-bg;
	height: 38px;
	-webkit-box-shadow: 0px 0px 0px 6px rgba(0, 0, 0, 1);
	-moz-box-shadow:    0px 0px 0px 6px rgba(0, 0, 0, 1);
	box-shadow:         0px 0px 0px 6px rgba(0, 0, 0, 1);
	z-index: -1;
	display: none;

	.autocomplete-highlight {
		color: $link-color;
	}

	@include respond-to("medium") {
		position: static;
	}
}

@include ie8 {
	.autocomplete-search-result {
		outline: 6px solid black;
	}
}

ul.ui-autocomplete {
	background: $main-menu-bg;
	width: 600px;
	border: none;
	max-height: 500px;
	overflow-y: auto;
	overflow-x: hidden;

	@include respond-to("medium") {
		width: 100% !important;
		max-width: 600px;
		top: 84px !important;
	}

	li.ui-menu-item {
		padding: 0;
		list-style-image: none;

		&.ui-state-focus {
			font-weight: normal;
			border: none;
			background: transparent;
			margin: 0;

			.item-result {
				.label {
					background-color: white;
					color: $link-color;
				}
			}

			a {
				background-color: $link-color;

				.autocomplete-highlight {
					color: white;
				}

				.label {
					.autocomplete-highlight {
						color: $link-color;
					}
				}
			}


			&.ui-category-name a,
			&.ui-menu-item-results a {
				background-color: transparent;
				text-decoration: underline;
			}
		}
		.ellipsis {
			display: block;
			text-overflow: ellipsis;
			white-space: nowrap;
			overflow: hidden;
		}

		a {
			display: block;
			padding: 10px 0;
			color: white;
			font-size: 14px;
		}
	}
}

.ui-category-name,
.ui-menu-item-results {
	background-color: #3d4855;
	font-size: 14px;
	cursor: default;

	&:hover, &.ui-state-focus {
		background-color: #3d4855 !important;
	}

	p, a {
		line-height: 30px;
	}

	p {
		color: white;
		margin-bottom: 0;

		span.text-highlighted {
			margin-right: 10px;
		}
	}

	a {
		display: inline !important;
		padding: 0 !important;
		color: $link-color !important;
		text-decoration: none;

		&:hover {
			background-color: transparent !important;
			text-decoration: underline;
		}
	}
}

.ui-menu-item-results {
	background-color: transparent;

	&:hover, &.ui-state-focus {
		background-color: transparent !important;
	}

	@include respond-to("medium") {
		.col-1-2 {
			width: 100%;
		}
	}
}

.item-result {
	@include respond-to("medium") {
		width: 100%;
	}

	.label {
		width: 55px;
		line-height: 25px;
		text-align: center;
		margin-top: 8px;
		padding: 0;
	}

	.ticker-item-title {
		float: left;
		width: 85%;
		padding-left: 15px;

		@include respond-to("medium") {
			padding-left: 0;
			width: 100%;
		}
	}
}


.ui-helper-hidden-accessible {
	display: none;
}
