$progress-bar-color: $amber !default;
$progress-bar-height: 1px !default;
$progress-bar-background: $white !default;

.progress-bar {
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	z-index: $z-index-10-progress-bar;
	width: 100%;
	height: $progress-bar-height;
	@include vendor-prefix(appearance, none);
	background-color: $progress-bar-background;

	/* Get rid of the default border in Firefox/Opera. */
	border: none;

	/* For IE10+, color of the progress bar */
	color: $progress-bar-color;

	&::-webkit-progress-value {
		background-color: $progress-bar-color;
	}

	&::-moz-progress-bar {
		background-color: $progress-bar-color;
	}

	&__container {
		position: fixed;
		display: block;
		width: 100%;
		top: 0;
		left: 0;
		height: $progress-bar-height;
		background-color: transparent;
	}

	&__bar {
		display: block;
		width: 100%;
		height: inherit;
		background-color: $progress-bar-color;
	}

}

@include ie9 {
	.progress-bar__container {
		position: absolute;
	}
}
