.login {
	&__wrapper {
		position: relative;
		z-index: 250;
		@extend .clearfix;

	}

	&__form {
		background-color: black;
		position: absolute;
		right: 0;
		top: 100%;
		width: 406px;
		padding: 20px;
		display: none;

		@include respond-to('small') {
			width: 100%;
		}

		.form__label {
			color: white;
		}

		&.active {
			display: block;
		}
	}
}
