a {
	color: $link-color;
	text-decoration: none;
	&:hover, &:focus {
		text-decoration: underline;
	}
	[class*="-link"] {
		display: inline-block;
		margin-left: 5px;
	}
}

.button {
	display: inline-block;
	color: #FFF;
	text-decoration: none;
	padding: 8px 12px;
	background: #fea100;
	margin-top: 6px;
	border:none;
	/* IE6-9 */
	-webkit-appearance: none;
	&:hover, &:focus {
		text-decoration: none;
		background: #f0b137; /* Old browsers */

	}

	&.no-bg, &.no-bg:hover, &.no-bg:focus {
		background: transparent;
		filter: none;
	}

	&.grey {
		background: #98a1ac; /* Old browsers */
		background: -moz-linear-gradient(top,  #98a1ac 0%, #7e8488 100%); /* FF3.6+ */
		background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#98a1ac), color-stop(100%,#7e8488)); /* Chrome,Safari4+ */
		background: -webkit-linear-gradient(top,  #98a1ac 0%,#7e8488 100%); /* Chrome10+,Safari5.1+ */
		background: -o-linear-gradient(top,  #98a1ac 0%,#7e8488 100%); /* Opera 11.10+ */
		background: -ms-linear-gradient(top,  #98a1ac 0%,#7e8488 100%); /* IE10+ */
		background: linear-gradient(to bottom,  #98a1ac 0%,#7e8488 100%); /* W3C */
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#98a1ac', endColorstr='#7e8488',GradientType=0 ); /* IE6-9 */

		&:hover, &:focus {
			background: #acb5bf; /* Old browsers */
			background: -moz-linear-gradient(top,  #acb5bf 0%, #848a91 100%); /* FF3.6+ */
			background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#acb5bf), color-stop(100%,#848a91)); /* Chrome,Safari4+ */
			background: -webkit-linear-gradient(top,  #acb5bf 0%,#848a91 100%); /* Chrome10+,Safari5.1+ */
			background: -o-linear-gradient(top,  #acb5bf 0%,#848a91 100%); /* Opera 11.10+ */
			background: -ms-linear-gradient(top,  #acb5bf 0%,#848a91 100%); /* IE10+ */
			background: linear-gradient(to bottom,  #acb5bf 0%,#848a91 100%); /* W3C */
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#acb5bf', endColorstr='#848a91',GradientType=0 ); /* IE6-9 */

		}
	}

	&.button--grad {
		/* Old browsers */
		background: -moz-linear-gradient(top, #e19217 0%, #c1720c 100%);
		/* FF3.6+ */
		background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #e19217), color-stop(100%, #c1720c));
		/* Chrome,Safari4+ */
		background: -webkit-linear-gradient(top, #e19217 0%, #c1720c 100%);
		/* Chrome10+,Safari5.1+ */
		background: -o-linear-gradient(top, #e19217 0%, #c1720c 100%);
		/* Opera 11.10+ */
		background: -ms-linear-gradient(top, #e19217 0%, #c1720c 100%);
		/* IE10+ */
		background: linear-gradient(to bottom, #e19217 0%, #c1720c 100%);
		/* W3C */
		filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#fea100', endColorstr='#c1720c',GradientType=0 );

		&:hover, &:focus {
			background: -moz-linear-gradient(top,  #f0b137 0%, #cf7601 100%); /* FF3.6+ */
			background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#f0b137), color-stop(100%,#cf7601)); /* Chrome,Safari4+ *z
			background: -webkit-linear-gradient(top,  #f0b137 0%,#cf7601 100%); /* Chrome10+,Safari5.1+ */
			background: -o-linear-gradient(top,  #f0b137 0%,#cf7601 100%); /* Opera 11.10+ */
			background: -ms-linear-gradient(top,  #f0b137 0%,#cf7601 100%); /* IE10+ */
			background: linear-gradient(to bottom,  #f0b137 0%,#cf7601 100%); /* W3C */
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f0b137', endColorstr='#cf7601',GradientType=0 ); /* IE6-9 */
		}
	}
}

.download-btn {
	display: block;
	position: relative;
	&:hover {
		text-decoration: none;
		span {
			color: $link-color;
		}
	}
	i {
		position: absolute;
		top: 1px;
		font-size: 32px;
	}
	span {
		display: block;
		padding-left: 40px;
		color: $text-color;
		font-size: 18px;
		line-height: 1;

		&.text-base {
			color: $base;
		}

		small {
			font-size: 13px;
		}
	}

	&--right {
		@include respond-to("up-than-large") {
			float: right;
		}
	}
}

.internal-header {
	.download-btn {
		padding-bottom: 12px;
		/* margin-top: -1px; */
		i {
			top: -3px;
		}
		span {
			line-height: .8;
		}
	}
}

.back-btn {
	float: left;
	display: block;
	position: relative;
	min-height: $icons-back-arrow_height;
	margin: 16px 0;
	&:hover {
		text-decoration: none;
		span {
			color: $link-color;
		}
	}
	i {
		position: absolute;
		top: 0;
		width: 33px;
		height: 33px;
		color: white;
		background-color: $link-color;
		text-align: center;

		&:before {
			line-height: 33px;
		}
	}
	span {
		display: block;
		padding-left: 45px;
		color: $text-color;
		font-size: 13px;
		line-height: 1.35;
	}

	&.dark-bg {
		span {
			color: white;
		}
	}
}

.back-link {
	color: $back-link-color;

	span {
		font-family: $main-font;
		font-size: 18px;
		color: inherit;
		line-height: 1.15;
	}
	.icons-arrow-left {
		font-size: 18px;
		float: left;
	}
}

.download-teaser-link {
	display: block;
	padding-left: 40px;
	font-size: 18px;
	color: $teaser-color;
	position: relative;
	&:hover {
		text-decoration: none;
	}
	.download-data {
		font-size: 13px;
	}
	.icons-teaser-download-link {
		position: absolute;
		left: 0;
		top: 4px;
	}
}
.download-link {
	display: block;
	padding-left: 25px;
	position: relative;

	.icons-download-small {
		position: absolute;
		left: 0;
		top: 4px;
	}
}

a.block-link {
	display: block;
	color: $text-color;
	&.block-link:hover,
	&.block-link:focus {
		text-decoration: none;
		.download-teaser-link {
			color: $link-color;
		}
		.text-primary {
			color: $link-color;
		}
	}
}

.icons-arrow-link {
	position: absolute;
	right: 0;
	bottom: 0;
	background-color: $arrow-button-bg;
	&:hover, &:focus {
		background-color: $highlightedbg;
	}

	&.orange {
		background-color: $link-color;
	}
}
a.back-btn {
	.icons-back-arrow {
		background-color: $highlightedbg;
		&:hover, &:focus {
			background-color: #f0b238;
		}
	}
	&:hover, &:focus {
		.icons-back-arrow {
			background-color: #f0b238;
		}
	}
}

.btn-simple {
	display: inline-block;
	border: none;
	background-color: transparent;
}

.block-button {
	position: relative;
	padding: 13px 0 13px 15px;
	//border-left: 5px solid $highlightedbg;
	background-color: $lk-gray;
	color: #FFF;
	font: 24px / 24px $main-font;
	display: block;
	.icons-arrow-link {
		background-color: $highlightedbg;
		top: 8px;
		right: 8px;
	}
	&:hover, &:focus {
		text-decoration: none;
		//border-left: 5px solid #f0b238;
		background-color: #8b9299;
		.icons-arrow-link {
			background-color: #e8a945;
		}
	}
}

.btn-arrowed {
	position: relative;
	display: block;
	height: 33px;
	padding: 0 6px;
	background-color: #6a7581;
	font: normal 18px $main-font;
	line-height: 33px;

	i {
		@extend .icons-arrow-right;
		font-size: 14px;
		position: absolute;
		top: 0;
		right: 0;
		display: block;
		width: 33px;
		height: 33px;
		background-color: $lk-gray;
		text-align: center;

		&:before {
			line-height: 33px;
		}
	}

	&:hover {
		text-decoration: none;
		i {
			background-color: $link-color;
		}
	}
}

.btn-icon-only {
	display: block;
	background: $link-color;
	text-align: center;
	width: 30px;
	height: 30px;

	i {
		font-size: 18px;
		&:before {
			line-height: 30px;
		}
	}

	&:hover {
		background-color: #f0b238;
	}
}

.btn-orange {
	background: $button-orange-bgc;
	color: white;
	padding: 10px;

	&:hover, &:focus, &:active {
		text-decoration: none;
	}

	&:hover {
		background-color: $button-orange-bgc-active;
	}

	i {
		font-size: 16px;
	}

	&.btn--small {
		padding: 5px 12px;
		font-size: 13px;
	}

	.icons-mail-share {
		padding: 0 4px;
	}
}
/*
.form__group .btn-orange {
	padding-top: 5px;
	padding-bottom: 5px;
} */

.btn-gray {
	background: #98a2ad;
	color: white;
	padding: 10px;

	&:hover, &:focus, &:active {
		text-decoration: none;
	}

	&:hover {
		background: lighten(#98a2ad, 10%);
	}

	i {
		font-size: 16px;
	}

	&.btn--small {
		padding: 5px 12px;
		font-size: 13px;
	}
}

.btn-video {
	width: 58px !important;
  	height: 36px !important;
  	line-height: 36px !important;
  	background-color: $button-orange-bgc !important;
  	text-align: center;
  	display: block;
  	border: none;
  	position: relative;
  	&:hover {
  		background-color: $button-orange-bgc-active !important;
  	}
  	&:before {
  		content:"" !important;
  		border: 6px solid transparent;
  		border-left: 11px solid #fff;
  		width: 0; height: 0;
  		margin: -6px 0 0 -5px !important;
  		display: block !important;
  		position: absolute;
  		top: 50%; left: 50%;
	}
	i {
		color: #FFF;
	}
}

.btn {
	border: none;
	display: inline-block;
}

.btn-login {
	color: white;
	padding: 5px 11px;

	i {
		margin-left: 5px;
		font-size: 8px;
	}

	&:hover, &:focus, &:active {
		text-decoration: none;
	}

	&.active {
		background-color: black;
	}
}

.active + .btn-login {
	background-color: black;
}
