html, body {
	height: 100%;
}

body {
	font-family: $basic-font;
	font-size: $font-size-base;
	line-height: $line-height-base;
	color: $text-color;
	margin: 0;
	//background: url(../img/bg.png) repeat-x $bg-color;
	background: $body-bgc;
	-webkit-overflow-scrolling : touch;

	&.overlay {
		overflow: hidden;
		/* position:fixed; */
		width: 100%;
	}
}

a {
	&:active,
	&:focus {
		text-decoration: none;
	}
}

// Reset fonts for relevant elements
input,
button,
select,
textarea {
	font-family: inherit;
	font-size: inherit;
	line-height: inherit;
}

select {
	@include vendor-prefix(appearance, none);
	@include vendor-prefix(border-radius, 0);
	color: $text-color;
	background-color: white;
}

img {
	display: block;
	&.inline {
		display: inline;
	}
}

figure {
	margin: 0;
	img {
		max-width: 100%;
		min-width: 100%;
	}
	figcaption {
		background: $darkbg-primary-text;
		padding: 12px;
		color: #FFF;
		a {
			color: #FFF;
		}
	}
}

.standalone {
	text-align: center;
	img {
		width: auto;
		display: inline;
		max-width: 100%;
		min-width: 1px;
	}
	figcaption {
		background: transparent;
		color: $text-color;
		width: 100%;
		text-align: center;
		a {
			color: $text-color;
		}
	}
}

input::-ms-clear {
    display: none;
}

progress::-webkit-progress-bar {
	background-color: transparent;
}
