$bar-color: $darkbg-primary-text;
$spinner-size: 6px;
.loader {
	font-size: $spinner-size;
	margin: 5em auto;
	// text-indent: -9999em;
	width: 11em;
	height: 11em;
	border-radius: 50%;
	background: $bar-color;
	background: -moz-linear-gradient(left, $bar-color 10%, rgba(255, 255, 255, 0) 42%);
	background: -webkit-linear-gradient(left, $bar-color 10%, rgba(255, 255, 255, 0) 42%);
	background: -o-linear-gradient(left, $bar-color 10%, rgba(255, 255, 255, 0) 42%);
	background: -ms-linear-gradient(left, $bar-color 10%, rgba(255, 255, 255, 0) 42%);
	background: linear-gradient(to right, $bar-color 10%, rgba(255, 255, 255, 0) 42%);
	position: relative;
	-webkit-animation: load3 1.4s infinite linear;
	animation: load3 1.4s infinite linear;

	&:before {
		width: 50%;
		height: 50%;
		background: $bar-color;
		border-radius: 100% 0 0 0;
		position: absolute;
		top: 0;
		left: 0;
		content: '';
	}
	&:after {
		background: white;
		width: 75%;
		height: 75%;
		border-radius: 50%;
		content: '';
		margin: auto;
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
	}

	&.loader--transparent:after {
		background-color: #293039;
	}
}

.internal-concept {
	.loader.loader--transparent:after {
		background-color: #f4f6f7;
	}
}

@-webkit-keyframes load3 {
	0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}

@keyframes load3 {
	0% {
		-webkit-transform: rotate(0deg);
		transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}

.lt-ie10 {
	.loader {
		position: relative;
		top: auto !important;
		left: auto !important;
		font-size: 16px;
		background: transparent;
		width: 100%;
		height: 100%;
		display: block;
		margin: 0 !important;
		text-align: center;

		&:before, &:after {
			display: none;
		}
	}

	.loader span {
		position: absolute;
		top: 50%;
		left: 50%;
		margin: -30px 0 0 -30px;
		display: block;
		width: 60px;
		height: 60px;
		background-image: url('data:image/gif;base64,');
		text-indent: -9999px;
	}
}
