.filter {
	color: white;

	.h5 {
		margin-top: 16px;
		margin-bottom: 0;
		font-size: 18px;
		font-family: $main-font;

		// @todo @theme
		// remove it if possible by adding a default color,
		// current whe have it color by .dark-bg p in cio-archive
		// make sure that dont broken in others scenarios
		@if $components-version == 'onlinebanking' {
			color: $white;
		}
	}

	&__row {
		background-color: $filter-row-bgc;
		padding-bottom: 10px;
	}

	&__container {
		position: relative;
		.filter__action-small {
			margin-top: 15px;
		}
	}

	&__choices {
		margin: 10px 0 0;
		min-height: 32px;
		padding: 2px 37px 0 5px;
		background-color: white;

		@extend .clearfix;
		@extend .unstyled;
	}

	&__input {
		height: 27px;
		border: none;
		padding: 0;
		margin: 0;
		color: #858585;
		width: 100%;
		outline: none;
	}

	&__search-button {
		@extend .icons-loup;
		position: absolute;
		width: 37px;
		height: 32px;
		line-height: 37px;
		top: 10px;
		right: 20px;
		font-size: 20px;
		text-align: center;
	}

	&__category {
		cursor: default;
		position: relative;

		&:hover, &:active, &:focus {
			text-decoration: none;
		}

		@include respond-to('medium') {
			cursor: pointer;
			display: block;
			padding: 10px;
			background-color: #677381;

			&:after {
				@include icons-arrow-down;
				position: absolute;
				top: 16px;
				right: 10px;
				display: block;
				font-size: 10px;
			}
		}

		&.filter-hide {
			font-size: 13px;
			.f-dropdown__arrow {
				@extend .icons-arrow-up;
				left: 100%;
				right: auto;
			}
		}
	}

	&__cat-small {
		display: none;
		color: $blue-gray;
		margin-left: 10px;

		/* @include respond-to('medium') {
			display: inline-block;
		} */
	}

	&__options {
		@extend .clearfix;
		@extend .unstyled;
		display: block;
		float: left;

		li {
			background-color: #3d4855;
			margin: 10px 10px 0 0;
			float: left;
		}
	}

	&__option {
		display: block;
		height: 100%;
		padding: 5px 8px;
		color: white;
		text-decoration: none;

		&.is-active {
			background-color: $filter-option-active;
		}

		&:hover {
			background-color: $filter-option-hover;
		}

		&:hover, &:focus, &:active {
			text-decoration: none;
		}
	}

	&__reset {
		&__close {
			@extend .icons-close;
			font-size: 0.65em;
			margin-left: 4px;
			background-color: #dc9223;
		    position: absolute;
		    top: 5px;
		    line-height: 1;
		}
		&.filter__action-small {
			padding-right: 25px;
		}
	}

	&__toggle {
		display: none;
		padding: 15px 0 5px;

		@include respond-to('medium') {
			display: block;
			padding-left: 12px;
		}
	}

	&__advanced {
		@include respond-to('medium') {
			max-height: 0;
			overflow: hidden;
			display: none;

			&--newsletter,
			&.is-opened {
				max-height: 9999px;
				display: block;
			}
		}
	}

	&__action-small {
		font-size: 11px;
		display: inline-block;
		position: relative;
		margin-bottom: 13px;
		margin-top: 0;

		.icon {
			font-size: 0.65em;
			margin-left: 4px;
			background-color: #dc9223;
		    position: absolute;
		    top: 6px;
		    line-height: 1;

		}

		.icons-arrow-right {
			left: 100%;
		}

		.icons-close {
		}
	}
}

// when chosen boots it creates another component, so
// this will not be visible, otherwhise this css will provide
// a similar look
.no-chosen {
	position: relative;

	.f-dropdown__arrow {
		z-index: 2;
		right: 12px;
		top: 8px;
		color: white;
	}

	&.theme-white .f-dropdown__arrow{
		color: $dark-gray;
	}

	&.theme-white .f-dropdown{
		background-color: white;
		color: $dark-gray;
		border: 1px solid $mid-gray;
	}

	&:focus,
	&:hover {
		.f-dropdown__arrow {
			@extend .icons-arrow-up;
			color: $amber;
		}
	}
}

$height: 32px;
.f-dropdown {
	$filter-item-space: 10px;
	position: relative;
	display: block;
	height: 100%;
	line-height: $height;
	background-color: $blue-gray;
	padding: 0 40px 0 10px;
	color: white;
	font-size: 14px;

	&:hover {
		color: $filter-dropdown-hover;
		text-decoration: none;
	}

	&:active, &:focus {
		text-decoration: none;
	}

	&.is-disabled {
		@include opacity(0.5);
		cursor: not-allowed;

		&:hover, &:active, &:focus {
			background-color: $blue-gray;
		}
	}

	&.is-active {
		margin-bottom: 0;
		height: 37px;
		border-bottom: 5px solid;
		background-color: #FFF;
		color: $filter-dropdown-active;
		border-color: #FFF;

		.f-dropdown__arrow {
			@extend .icons-arrow-up;
		}
	}

	&__wrapper {

		height: $height;
		margin: 10px 0;

		@extend .clearfix;
		@extend .unstyled;

		li {
			float: left;
			width: auto;
			margin-right: $filter-item-space;

			@include respond-to('medium') {
				width: 100%;
				margin-bottom: 10px;
			}
		}

		&--newsletter {
			height: $height;
			margin: 10px 0;

			@extend .clearfix;
			@extend .unstyled;
		}
	}

	&__arrow {
		@extend .icons-arrow-down;
		position: absolute;
		top: 3px;
		right: 4px;
		width: 30px;
		height: $height;
		text-align: center;
		font-size: 13px;

		&.theme-white{
			fill: #000;
		}
	}

	&__options {
		background-color: white;
		overflow: hidden;
		height: 0;
		position: relative;
		overflow: hidden;

		p {
			margin: 0;
		}

		.row:first-child {
			padding-top: 10px;
		}

		&.is-opened {
			width: 100%;
			margin-top: -5px;
			margin-bottom: 20px;
		}

		.f-dropdown__option {
			color: #4f5356;
			text-decoration: none;
			width: 25%;
			float: left;
			padding-left: 20px;
			white-space: nowrap;
		    text-overflow: ellipsis;
		    overflow: hidden;

		    @include respond-to("large") {
		    	width: 50%;
		    }
		}

		@include respond-to("large") {
			.col-lg-6 {
				margin-left: 0;
			}
		}
	}

	&__option {
		display: block;
		padding: 10px 0;
		margin: 0;
		line-height: 1;


		&.is-active {
			color: $filter-dropdown-active;

			&:after {
				@include icons-success;
				margin-left: 8px;
				font-size: 10px;
				position: absolute;
			}
		}

		&:hover {
			color: $filter-dropdown-hover;
			text-decoration: none;
		}
	}

	&__actions {
		padding: 10px 0 25px;
		.btn-orange {
			padding: 8px 16px;
		}
	}
}

.f-tag {
	@extend .label;
	@extend .label-grey;
	@extend .nobreak;

	&__wrapper {
		margin-bottom: 8px;

		@extend .clearfix;
		@extend .unstyled;

		li {
			display: block;
			margin: 2px 2px 2px 0;
			position: relative;
			overflow: hidden;
			//vertical-align: top;
			line-height: 19px;
			float: left;
		}
	}

	&__close {
		@extend .icons-close;
		font-size: 0.65em;
		margin-left: 4px;
	}
}


.f-overlay {
	position: fixed;
	top: 100%;
	left: 0;
	z-index: 0;
	height: 100%;
	width: 100%;
	visibility: hidden;
	background-color: $pale-gray;
	z-index: 9999;

	&__header {
		position: relative;
		min-height: 48px;
		border-bottom: 1px solid #d2deea;
	}

	&__apply, &__close {
		position: absolute;
		top: 7px;
		display: block;
		height: 33px;
		width: 33px;
		text-align: center;
		font-size: 14px;
		line-height: 34px;
	}

	&__apply {
		right: 7px;
		@extend .icons-apply-badge;
	}

	&__close {
		background-color: #98a2ad;
		left: 7px;
		color: white;

		@extend .icons-close;
	}

	&__title {
		text-align: center;
		line-height: 48px;
		font-size: 18px;
		margin-bottom: 0;
		color: $text-color;
	}

	&__list {
		display: block;
		overflow-y: auto;

		@extend .unstyled;
		@extend .clearfix;
	}

	&__item {
		text-align: center;
		display: block;
		padding: 10px 0;
		color: $base;
		text-decoration: none;
		font-size: 16px;
		line-height: 1.75;

		&:hover, &:active, &:focus {
			text-decoration: none;
		}

		&.is-active {
			background-color: $filter-dropdown-active;
			color: white;
		}
	}

	&.opened {
		visibility: visible;
	}
}
