$sharing-tweet-theme-1-color: $white !default;
$sharing-tweet-theme-2-color: #4c5255 !default;


//
// Single tweet button
//
.sharing__tweet {
	margin-left: 3px;
}

.sharing .sharing__tweet .fa-twitter {
	font-size: 16px;
}

.sharing .sharing__tweet .shariff {
	.shariff-button {
		margin: 0
	}

	.orientation-horizontal {
		background: transparent;
		padding: 0;
	}

	.shariff-button .share_text {
		display: inline-block;
		position: relative;
		top: -4px;
		margin-left: 8px;
		font-size: 11px;
	}

	.shariff-button .share_text:hover {
		text-decoration: underline;
	}
}


//
// Themes
//

//
// White
//
.shariff .theme-1 {

	.twitter:hover,
	.twitter a,
	.twitter a:hover {
		color: $sharing-tweet-theme-1-color !important;
	}
}


//
// Dark
//
.shariff .theme-2 {

	.twitter:hover,
	.twitter a,
	.twitter a:hover {
		color: $sharing-tweet-theme-2-color !important;
	}
}

