table {
	// margin-bottom: 20px;
}

table th {
	font-weight: bold;
}

table td, table th {
	padding: 9px 10px;
	text-align: left;
}

.responsive {
	padding: 0;
}

.drag-tip {
	display: none;
}

/* Mobile */
@include respond-to("large") {
	table.responsive {
		position: relative;
		margin-bottom: 0;
	}
	.pinned {
		position: absolute;
		left: 0;
		top: 0;
		background: #FFF;
		width: 50%;
		overflow: hidden;
		overflow-x: scroll;
		@include vendor-prefix(box-shadow, 1px 0px 10px -2px #000);
	}
	.pinned table {
		border-right: none;
		border-left: none;
		width: 100%;
	}
	.pinned table th, .pinned table td {
		white-space: nowrap;
	}
	.pinned td:last-child {
		border-bottom: 0;
	}
	/* 	.pinned td:first-child + td, .pinned th:first-child + th { display: inherit !important; }
	 */
	div.table-wrapper {
		position: relative;
		margin-bottom: 10px;
		overflow: hidden;
		border-right: 1px solid transparent;
	}
	div.table-wrapper div.scrollable {
		margin-left: 50%;
	}
	div.table-wrapper div.scrollable {
		overflow: scroll;
		overflow-y: hidden;
	}
	table.responsive td, table.responsive th {
		position: relative;
		white-space: nowrap;
		overflow: hidden;
	}
	table.responsive th:first-child,
	table.responsive td:first-child,
	table.responsive td:first-child,
	table.responsive.pinned td,
	table.responsive th:first-child + th,
	table.responsive td:first-child + td,
	table.responsive td:first-child + td {
		display: none;
	}

	table.responsive th > table th:first-child,
	table.responsive th > table td:first-child,
	table.responsive th > table td:first-child + td {
		display: table-cell;
	}

	.table-tips {
		position: absolute;
		left: 0;
		width: 100%;
		.drag-tip {
			text-align: center;
			padding: 13px 17px 10px 16px;
			display: block;
			position: absolute;
			top: 100px;
			left: 0;
			margin-left: 60%;
			opacity: 0;
			z-index: 10;
			background-color: rgba($hr-dark, .8);
			color: #FFF;
			font-size: 11px;
			line-height: 1.3;
			max-width: 117px;
			.icons-hand-drag {
				font-size: 60px;
				line-height: .85;
				display: block;

				-webkit-animation-duration: 1s;
			  	-webkit-animation-name: handdrag;
			  	-webkit-animation-iteration-count: infinite;
			  	-webkit-animation-direction: alternate;
			  	-webkit-animation-timing-function:linear;

			  	animation-duration: 1s;
			  	animation-name: handdrag;
			  	animation-iteration-count: infinite;
			  	animation-direction: alternate;
			  	animation-timing-function:linear;
			}
		}
	}
}

@include respond-to("medium") {
	.table-tips .drag-tip {
		margin-left: 35%;
	}

	div.table-wrapper {
		div.pinned {
			width: 21%;
		}
		div.scrollable {
			margin-left: 21%;
		}
	}

	table.responsive th:first-child + th,
	table.responsive td:first-child + td,
	table.responsive td:first-child + td {
		display: block;
	}
	.table-tips {
		.slide-icon {
			left: 60%;
		}
	}
}


@include respond-to("small") {
	div.table-wrapper {
		div.pinned {
			width: 30%;
		}
		div.scrollable {
			margin-left: 30%;
		}
	}
}

.js-show-and-hide {
	-webkit-animation-duration: 4s;
  	-webkit-animation-name: fadeout;

  	animation-duration: 4s;
  	animation-name: fadeout;
}

@keyframes fadeout {
	0% {
		opacity: 1;
		visibility: visible;
	}

	90% {
		opacity: 1;
		visibility: visible;
	}

	100% {
		opacity: 0;
		visibility: hidden;
	}
}

@-webkit-keyframes fadeout {
	0% {
		opacity: 1;
		visibility: visible;
	}

	90% {
		opacity: 1;
		visibility: visible;
	}

	100% {
		opacity: 0;
		visibility: hidden;
	}
}

@keyframes handdrag {
	0% {
		transform: rotate(5deg);
	}

	55% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(-10deg);
	}
}

@-webkit-keyframes handdrag {
	0% {
		-webkit-transform: rotate(5deg);
	}

	55% {
		-webkit-transform: rotate(0deg);
	}

	100% {
		-webkit-transform: rotate(-10deg);
	}
}
