.popbox {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background-color: rgba(#000, .8);
	z-index: -1;
	visibility: hidden;
	pointer-events: none;
	/* display: none; */

	@include opacity(0);
	@include vendor-prefix(transition, all 0.25s linear);

	iframe {
		display: none;
	}

	&.is-open {
		visibility: visible;
		/* display: block; */
		pointer-events: auto;
		z-index: 9999;

		@include opacity(1);

		.popbox__wrapper {
			@include opacity(1);
		}

		iframe {
			display: block;
		}
	}

	&__wrapper {
		margin: 30px auto;
		max-width: 817px;
		position: relative;
		-webkit-overflow-scrolling: touch;
		// height: 400px;

		@include vendor-prefix(transition, all 0.30s linear);
		@include vendor-prefix(transition-delay, .20s);
		@include opacity(0);
		@include respond-to('xlarge') {
			padding-left: 20px;
			padding-right: 20px;
		}

		&--big {
			max-width: 980px;
		}

	}

	&__content {
		overflow-x: hidden;
		overflow-y: auto;
		height: 95%;
		@extend .clearfix;
	}

	&__close {
		position: absolute;
		right: -20px;
		top: -10px;
		padding: 20px;
		font-size: 16px;

		@include respond-to('xlarge') {
			right: 0px;
		}
	}

	&__footer {
		@extend .clearfix;

		.btn {
			margin-left: 5px;
		}
	}
}
