$rate-default-bgc: $white !default;
$rate-read-only-color: $text-color !default;

$rate-voting-color: $white !default;
$rate-voting-bgc: $amber !default;

.rate {
	min-height: 104px;
	padding: 20px 20px 10px 20px;
	width: 100%;
	float: left;
	background: $rate-default-bgc;

	&__headline {
		display: block;
		font-weight: bold;
		margin-bottom: 9px;
		line-height: 1.2;
	}

	// *1 @note: !important to replace the default plugin width
	// *2 @note: avoid break anchor
	&__icons {
		@extend .clearfix;
		width: 100% !important; // *1
		display: inline-block; // *2
	}

	&__icons img {
		float: left;
		margin-right: 13px;
	}

	&__total {
		position: relative;
		top: -4px;
		font-size: 11px;
		padding-left: 2px;
	}

	&__score {
		position: relative;
		top: -2px;
	}

	&__score,
	&__total {
		display: inline-block;
	}

	&__hint {
		float: left;
		font-size: 11px;
	}

	&--overview {
		margin-right: 1%;
		color: $rate-read-only-color;
	}

	&__anchor,
	&__anchor img {
		cursor: pointer;
	}

	&__message {
		float: right;
		font-size: 11px;
	}

	@include respond-to("medium-and-up") {
		width: 49.5%;
	}

	@include respond-to("up-than-large") {
		padding-left: 12px;
		width: 100%;
		float: none;

		&__headline {
			margin-bottom: 19px;
		}

		&__icons {
			display: inline-block;
			padding-left: 2px;
		}

		&__icons img {
			margin-right: 8px;
		}

		&--overview &__icons {
			margin-right: 0;
			// @note: !important to replace the default plugin width
			width: 132px !important;
		}

		&__score {
			padding-left: 4px;
		}

		&__total {
			min-width: 43px;
		}
	}
}

.rate.rate--voting {
	@extend .clearfix;
	color: $rate-voting-color;
	background: $rate-voting-bgc;
}
