.list-style-1 { list-style: square inside; }

.list-1 {
	padding-left: 16px;
	li {
		position: relative;
		margin-bottom: 20px;

		.list-content {
			.tags {
				margin-bottom: 10px;
			}
		}

		&:last-child {
			margin-bottom: 0;
		}
	}
}

.list-2 {
	li {
		padding-top: 8px;
		border-top: 1px solid #b9b9ba;

		&:first-child {
			padding-top: 0;
			border-top: none;
		}
	}
}

.list-3 {
	@extend .list-2;
	li {
		padding-bottom: 8px;
	}
}

.list-4 {
	margin: 20px 0 30px;

	li {
		margin-bottom: 14px;
		display: inline-block;
		@include respond-to("medium") {
			display: block;
		}
		margin-right: 20px;
		&:last-child {
			margin-right: 0;
		}

		i {
			margin-right: 5px;
		}
	}
}

.list-5 {
	& > li {
		border-bottom: 1px solid #d8dde1;
		padding: 8px 0;
	}
}

.list-6 {
	padding-left: 0;
	list-style: none;

	li {
		position: relative;
		border-top: 1px solid #b9b9ba;
		padding-top: 12px;
		padding-bottom: 12px;
	}

	.icons-square {
		font-size: 5px;
		position: absolute;
	}

	p {
		padding-left: 10px;
	}
}


ul.lined-list {
	padding: 0;
	margin: 0 0 4px 0;
	li {
		padding: 0 0 0 20px;
		position: relative;
		margin: 0 0 4px 0;
		list-style: none;
	}
	.icons-lined-list {
		left: 0;
		top: 8px;
		position: absolute;
	}
}

ol {
	padding: 0 0 0 20px;
	margin: 0 0 4px 0;
	li {
		list-style-position: outside;
		margin: 0 0 6px 6px;
	}
}

ul.short-list,
ol.short-list {
	margin: 0 0 8px 0;
	li {
		margin: 0;
	}
}

.js-odd-even {
	li:nth-child(odd) {
		clear: both;
		margin-left: 0
	}
}
@include ie8 {
	li.el-even {
		clear: both;
		margin-left: 0
	}
}

.cols-list {
	display: table;
	width: 100%;

	.cols-list-item {
		display: table-cell;
		border-top: 1px solid $hr-content;
		padding-left: 10px;

		a {
			display: block;
			position: relative;
			height: 100%;
			padding-left: 45px;

			i {
				position: absolute;
				top: 4px;
				left: 0px;
			}
		}

		&:first-child {
			padding-left: 0;
		}
	}

	&:first-child {
		.cols-list-item {
			border-top: 0;
		}

		@include respond-to("medium") {
			.cols-list-item {

				border-top: 1px solid $hr-content;

				&:first-child {
					border-top: 0;
				}
			}
		}
	}

	&.col2 {
		.cols-list-item {
			width: (100%/2);
		}
	}

	&.col3 {
		.cols-list-item {
			width: (100%/3);
		}
	}

	@include respond-to("large") {
		display: block;

		.cols-list-item {
			display: block;
			padding: 10px 0;
		}

		&.col2,
		&.col3 {
			.cols-list-item {
				width: 100%;
			}
		}
	}
}

.tags-list {
	padding-top: 5px;
	color: white;

	.label {
		display: inline-block;
		margin-right: 4px;
		margin-bottom: 4px;
	}
}



.icon-list-demonstration {
	margin-top: 0;
	margin-bottom: 0;
	padding-left: 0;
	list-style: none;

	li {
		@extend .clearfix;
		display: block;
		clear: both;
		margin-bottom: 20px
	}

	span, i {
		display: block;
		float: left;
	}

	i {
		margin-right: 20px;
	}

	span {
		padding-left: 3px;
		line-height: 1.6;
	}
}
