.step {
	@extend .list-inline;
	display: inline-block;
	margin-left: 100px;
	position: relative;
	z-index: 1;

	@include respond-to('small') {
		width: 85px;
		margin: 0;
	}

	&:first-child {
		margin-left: 0;

		.step__icon {
			padding-left: 0;
		}
	}

	&:last-child {
		&:before {
			content: '';
			display: block;
			position: absolute;
			width: 100%;
			height: 100%;
			background-color: #F4F6F7;
		}
	}

	&__icon {
		height: 75px;
		//width: auto;
		display: block;
		margin-bottom: 20px;
		z-index: 10;
		position: relative;

		@include respond-to('small') {
			height: 40px;
		}

		&--fallback {
			display: none;
		}
	}

	&--active {
		.step__icon {
			color: $amber;
		}

		.step-fill-2 {
			fill: $amber;
		}
	}

	&__list {
		@extend .unstyled;
		position: relative;
		display: inline-block;
		padding-left: 0;

		&:before {
			content: '';
			width: 100%;
			height: 1px;
			background-color: #d8dde1;
			position: absolute;
			top: 50px;
			left: 0;
			z-index: 0;

			@include respond-to('small') {
				top: 22px;
			}
		}
	}

	&__desc {
		position: relative;
		color: #858585;
		z-index: 10;

		@include respond-to('small') {
			font-size: 11px;
		}
	}

	&.step--1 {
		.step__icon {
			width: 61px
		}
	}
	&.step--2 {
		.step__icon {
			width: 88px
		}
	}
	&.step--3 {
		.step__icon {
			width: 74px
		}
	}
}

.step-fill-1 {
	fill: #F4F6F7;
}

.step-fill-2 {
	fill: #b4b6bb;
}

//fallbacks

@include ie8 {
	.step__list:before {
		display: none;
	}
}

.no-svg .step__icon {
	display: none;
}

.no-svg .step__icon--fallback {
	display: block;
}

