.side-teaser {
	&__image {
		width: 100%;
	}

	&__limiter {
		padding: 20px;
	}

	&--dark {
		background-color: $contentdarkbg;

		.side-teaser__title, a, p {
			color: white;
		}
	}

	&__title {
		margin-bottom: 10px;
	}

	&__link {
		&:hover {
			color: $amber;
			text-decoration: none;
		}
	}

}
