.pagination-wrapper {
	background-color: #e6e9eb;

	.results-number {
		display: block;
		padding: 20px 0 17px;
		line-height: 1;
	}

	.text-center > .results-number {
		position: absolute;
	}

	.show-all {
		display: block;
		@extend .pull-right;
		color: $text-primary;
	}

	&.no-bg {
		background-color: transparent;
		color: $paginator-color;

		a {
			color: $paginator-color;
		}
	}

	&.dark-bg {
		background-color: $contentdarkbg;
		.results-number, .paginator li a {
			color: $base;
		}

		.paginator .active a {
			color: $paginator-color-active;
		}

		.paginator li {
			.icons-arrow-right-dark,
			.icons-double-arrow-right-dark,
			.icons-arrow-left-dark,
			.icons-double-arrow-left-dark {
				&:hover {
					color: $paginator-color;
				}
			}
		}
	}
}

.paginator {
	display: inline-block;
	margin: 0 auto;
	padding: 20px 0 17px;
	text-align: center;
	list-style: none;
	vertical-align: middle;

	li {
		float: left;
		line-height: 14px;
		margin-left: 15px;

		&.active, &:hover {
			a {
				color: $link-color;

				&:hover {
					text-decoration: none;
				}
			}
		}
		&.active {
			a:hover {
					cursor: default;
			}
		}

		a {
			color: $text-primary;
		}

		&:first-child {
			margin-left: 0;
		}

	}

	li:first-child + li {
		margin-left: 5px;
	}
	li:last-child {
		margin-left: 5px;
	}
	li:first-child a,
	li:last-child a {
		font-size: 13px;
	}

	li:first-child a,
	li.previous a,
	li:last-child a,
	li.next a {
		color: $text-color;
	}

	.icons-arrow-right-dark {
		@extend .icons-arrow-right;
		&:hover {
			color: $link-color;
		}
	}

	.icons-double-arrow-right-dark {
		@extend .icons-double-arrow-right;
		&:hover {
			color: $link-color;
		}
	}

	.icons-arrow-left-dark {
		@extend .icons-arrow-left;
		&:hover {
			color: $link-color;
		}
	}

	.icons-double-arrow-left-dark {
		@extend .icons-double-arrow-left;
		&:hover {
			color: $link-color;
		}
	}

	@include respond-to("medium") {
		li {
			display: none;

			&.active {
				display: block;
				text-align: center;
			}

			&.previous {
				margin-left: 0;
			}

			&.previous, &.next {
				display: block;
			}
		}
	}
}

.pagination-wrapper.no-bg {
	.paginator {
		li:first-child,
		li.previous,
		li:last-child,
		li.next {
			a {
				color: $paginator-color;
			}

			&.disabled {
				a {
					color: #8996a0;

					&:hover {
						text-decoration: none;
					}
				}
			}
		}

		.icons-arrow-right-dark,
		.icons-double-arrow-right-dark,
		.icons-arrow-left-dark,
		.icons-double-arrow-left-dark {
			&:hover {
				color: $paginator-color;
			}
		}
	}
}

.item-number {
	padding: 25px 0;
	line-height: 14px;
	ul {
		display: inline-block;
		margin-right:6px;
	}
	li {
		display: inline-block;
		line-height: 14px;
		margin-left: 15px;
		a {
			color: $text-primary;
			&:hover, &:focus, &.active {
				color: $link-color;
			}
		}
		@include respond-to("large") {
			margin-left: 5px;
		}
	}
	span {
		white-space: nowrap;
		float:right;
	}

	@include respond-to("small") {
		display: none;
	}
}

@media (max-width: 900px) {
	.item-number {
		span {
		    display: none;
		}
	}
}

/* IE 7 - 8 */
@include ie8 {
	.pagination-wrapper.row {
		float:left;
		width:100%;
	}
}
