$btn-w: 68px;
$btn-w-lg: 32px;
$sticker-w: 380px;

@mixin sticker-toggle-btn {
	display: block;
	position: absolute;
	bottom: 50px;
	left: -11px;
	font-size: 11px;
	@include vendor-prefix(transform, rotate(-90deg));
}


.stickers {
	position: fixed;
	top: 0;
	right: 0;
	z-index: 999;
	height: 100%;
	width: 1px;
}

.sticker {
	position: absolute;
	top: 0;
	right: 0;
	width: $sticker-w;

	.social-bar__wrapper {
		display: none;

		@include respond-to("medium") {
			display: inline-block;
		}
	}



	&.opened {
		width: $sticker-w;

		.sticker-toggle {
			i {
				@extend .icons-close;
				font-size: 16px;
				bottom: 5px;
			}
		}
		.sticker-wrapper {
			width: $sticker-w - $btn-w;
		}
	}

	&.inactive {
		.sticker-toggle {
			background-color: #8996a0;
			border-left: 1px solid $mid-gray-2;
		}
	}

	p, .text-primary, .h6 {
		color: white;
	}
}

.sticker-wrapper {
	position: absolute;
	top: 0;
	right: $btn-w-lg;
	width: 0;
	background-color: $link-color;
	overflow: hidden;
	@include vendor-prefix(transition-property, width);
	@include vendor-prefix(transition-duration, 0.5s);


	.sticker-content {
		$padding-lr: 35px;
		padding: 30px $padding-lr;
		width: $sticker-w - $btn-w - $padding-lr * 2;

		@include respond-to("large") {

			.space {
				display: none;
			}
		}

		.h6 {
			font-size: 20px;
			line-height: 22px;
		}

		a {
			color: white;
		}

	}

	.hr {
		background-color: #efa530;
	}

}

.sticker-toggle {
	position: absolute;
	top: 0;
	right: 0;
	background: $sticker-bgc;
	width: $btn-w-lg;
	height: $btn-w-lg;
	color: white;
	text-decoration: none;
	text-align: center;
	cursor: pointer;
	span {
		font-size: 9px;
	}

	i {
		position: absolute;
		bottom: 7px;
		@include vendor-prefix(transform, rotate(-90deg));
	}

	@include respond-to("large") {
		width: $btn-w-lg;
		height: 100px;
		span {
			@include sticker-toggle-btn;
		}
	}
}

.sticker-contact {
	top: 190px;

	@include respond-to("medium") {
		display: none !important;
	}

	.sticker-toggle {
		width: $btn-w-lg;
		height: 100px;
		border-left: 1px solid $sticker-bc;

		span {
			@include sticker-toggle-btn;
		}

		i {
			margin: 0;
			position: absolute;
			bottom: 5px;
			left: 10px;
			font-size: 15px;
		}

	}
}

.sticker-newsletter {
	@extend .sticker-contact;

	@include respond-to("medium") {
		display: none !important;
	}

	top: 320px;

	&.opened {
		.sticker-wrapper {
			width: 372px;
		}
	}

	.sticker-wrapper {
		.sticker-content {
			width: 302px;
			padding: 25px 30px;
		}
	}

	.sticker-toggle {
		height: 126px;
		border-left: 1px solid $sticker-bc;
		span {
			left: -22px;
			bottom: 63px;
		}


		i {
			left: 10px;
			bottom: 7px;
			font-size: 17px;
		}
	}

}
@-moz-document url-prefix() {
	.sticker-newsletter {
		.sticker-toggle {
		    span {
		    	left: -19px !important;
		    }
		}
	}
}
.sticker-up {
	width: $btn-w-lg;
	height: 32px;
	top: auto;
	bottom: 20%;

	@include respond-to("medium") {
		display: block !important;
	}

	.sticker-toggle {
		height: $btn-w-lg;
		i {
			@include vendor-prefix(transform, rotate(0));
			position: absolute;
			bottom: auto;
			top: 6px;
			left: 10px;
			font-size: 12px;
		}
		@include respond-to("medium") {
			opacity: 0.5;
				&:hover, &:focus {
				opacity: 1;
			}
		}
	}
}


@include ie8 {
	.sticker-contact {
		.sticker-toggle {
			background: #e29317 url("../img/sticker-IE8.png") no-repeat 0 0;

			i ,span {
				display:none;
			}
		}
		&.opened {
			.sticker-toggle {
				background: #e29317 url("../img/sticker-IE8-opened.png") no-repeat 0 0;

				i ,span {
					display:none;
				}
			}


		}
	}

	.sticker-newsletter {
		.sticker-toggle {
			background:  #e29317 url("../img/sticker-newsletter-IE8.png") no-repeat 0 0;
		}

		&.opened {
			.sticker-toggle {
				background:  #e29317 url("../img/sticker-newsletter-IE8-opened.png") no-repeat 0 0;

			}
		}
	}
}

.fixfixed {
	.stickers {
		position: absolute;
	}
}

.sticky-footer-wrap {
	position: fixed !important;
	bottom: 0;
	left:5px;
	right:5px;
	box-shadow:  0px -3px 7px 0px rgba(0, 0, 0, 0.3);
	.button {
		margin-bottom: 4px;
	}
}

.sticky-actions {
	position: fixed;
	bottom: 0;
	left: 0;
	right: 0;
	margin: 0 auto;
	padding-left: 20px;
	padding-top: 20px !important;
	padding-bottom: 20px !important;
	background-color: $lk-gray;
	width: 100%;
	width: calc(100% - 40px);
	max-width: 980px;
	min-width: 300px;
	z-index: 100;
	backface-visibility: hidden;
	-webkit-font-smoothing: antialiased;

	@include vendor-prefix(box-sizing, border-box);

	@include respond-to('medium') {
		left: 5px;
		right: 5px;
		width: calc(100% - 10px);
	}

	&__wrapper {
		position: relative;

	}

	&--static {
		position: static;
		width: 100%;
	}
}


.sticker-issues {
	top: auto;
	bottom: 50%;

	@include respond-to("large") {
		display: none !important;
	}

	.sticker-toggle {
		border-left: 1px solid $sticker-bc;
	}

	.sticker-wrapper {
		background: transparent;
	}

	.sticker-wrapper .sticker-content {
		padding: 0;
		height: 110px;
	}

	&.opened .sticker-wrapper {
		width: 242px;
	}

	.text-uppercase {
		width: 112px;
		left: -39px !important;
	}

	.sticker-toggle {
		height: 124px;
		span {
			@include sticker-toggle-btn;
			bottom: 62px;
		}

		i {
			font-size: 17px;
		}
	}

	.icons-arrow-left-and-right {
		position: absolute;
		right: -5px;
		left: 0;
		bottom: 5px;
		font-size: 17px
	}

	&.opened {
		.icons-arrow-left-and-right {
			bottom: 4px;
		}
	}
}

.sticker--icon {
	position: absolute;
	@include vendor-prefix(transform, rotate(0));

	&.icons-phone {
		right: 9px;
	}
}
.sticker-issues--prev {
	border-bottom: 1px solid $sticker-bc;
}

.sticker-issues--prev,
.sticker-issues--next {
	display: block;
	clear: both;
	height: 62px;
	background-color: $sticker-bgc;
	border-right: 1px solid $sticker-bc;

	&:hover {
		background-color: $sticker-bgc-hover;
	}

	i,
	span {
		display: block;
		float: left;
	}

	i {
		padding: 18px 17px 7px 18px;
		&:before {
			font-weight: bold;
			-webkit-font-smoothing: subpixel-antialiased;
			-moz-osx-font-smoothing: auto;
			//text-rendering: auto;
		}
	}

	span {
		padding-top: 13px;
		padding-bottom: 13px;
		width: 180px;
		line-height: 1.2;
	}
}

.sticker-issues.mobile {
	position: relative;
	overflow: hidden;
	width: 100%;

	@include respond-to("large") {
		display: block !important;
	}

	@include respond-to("up-than-large") {
		display: none;
	}

	.sticker-toggle {
		display: block;
		position: static;
		margin-left: auto;
		margin-right: auto;
		width: 100%;
		height: 32px;
		line-height: 32px;
		border-bottom: 1px solid $sticker-bc;
		border-left: none;
	}

	.text-uppercase {
		position: static;
		margin-left: 7px;
		font-weight: 100;
		@include vendor-prefix(transform, rotate(0));
	}

	.icons-arrow-left-and-right {
		display: none;
	}

	.sticker-wrapper {
		position: static;
		width: 100%;
		margin-left: auto;
		margin-right: auto;
	}

	.sticker-content {
		width: 100%;
		height: auto;
		background-color: $sticker-bgc;
		position: relative;
		display: table;
	}

	.sticker-issues--prev,
	.sticker-issues--next {
		width: 50%;
		clear: none;
		display: table-cell;
		/* float: left; */
		height: auto;
		vertical-align: top;
		i {
			/* padding: 19px 24px 27px 9px; */
			width: 30%;
			text-align: center;
		}
	}

	.sticker-issues--prev {
		i {
			padding: 19px 9px 27px 24px;
		}

		span {
			padding-right: 15px;
		}
	}

	.sticker-issues--next {
		span {
			padding-left: 15px;
		}

		.sticker--prev  {
			float: right;
		}

		i {
			padding: 19px 24px 27px 9px;
		}

		border-bottom: 1px solid $sticker-bc;
		border-right: none;
	}

	.sticker-issues--prev span,
	.sticker-issues--next span {
		width: 70%;
	}
}
