$colors: orange $link-color,
dark $text-color,
white white,
black black,
red $negative,
gray $gray,
gray-2 $mid-gray-2,
pantone-8180 #7c8796,
pale-gray $pale-gray,
copy-text-gray $text-color;

@each $color in $colors {
	$key: nth($color, 1);
	$value: nth($color, 2);

	.#{$key} {
		color: $value !important;
	}

	.hover-#{$key} {
		&:hover {
			* {
				color: $value !important;
			}
			color: $value !important;
		}
	}
}

$bgcolors: orange #e39318,
mid-orange #eaad52,
light-orange #efc98b,
blue #001645,
mid-blue #6e7794,
light-blue #a4a7ba,
gray #768998,
light-gray #d9e3e5,
white white,
black black,
pantone-8180 #7c8796,
pale-gray $pale-gray,
copy-text-gray $text-color;

@each $bgcolor in $bgcolors {
	$key: nth($bgcolor, 1);
	$value: nth($bgcolor, 2);

	.bg-#{$key} {
		background-color: $value;

		&.arrow-bottom, &.arrow-right, &.arrow-top {
			&:after {
				border: solid transparent;
				border-color: transparent;
			}
		}

		&.arrow-bottom {
			&:after {
				border-top-color: $value !important;
			}
		}

		&.arrow-right {
			&:after {
				border-left-color: $value !important;
			}
		}

		&.arrow-top {
			&:after {
				border-bottom-color: $value !important;
			}
		}
	}
}

// In db-theme there's elements that should be orange
// Since the '.orange' class has a blue color in this theme
// it's necessary a modifier to keep orange no matter the theme
// @note @theme
.always-orange {
	color: $amber !important;
}

// Generate  light version of palette colors
// mid-gray-100: #xxxxxx; mid-gray-80: #xxxxxx; mid-gray-60: #xxxxxx; mid-gray-40: #xxxxxx;  mid-gray-20: #xxxxxx;
$palette: mid-gray #8996a0,
dark-gray #666666,
warm-gray #988f86,
dark-blue #002244,
blue-gray #3d4855,
amber #e29317,
bright-blue #0098DB,
green #7AB800,
purple #57068C,
pink #d71f85,
bright-red #e00034;

@each $color in $palette {
	$key: nth($color, 1);
	$value: nth($color, 2);

	.#{$key} { color: $value; };

	@for $i from 1 through 4 {
		$percentage: $i  * 20%;
	  .#{$key}-#{$i  * 20} { color: mix($value, white, $percentage);}
	}
}

@each $bgcolor in $palette {
	$key: nth($bgcolor, 1);
	$value: nth($bgcolor, 2);

	.bg-#{$key} { background-color: $value; };

	@for $i from 1 through 4 {
		$percentage: $i  * 20%;
	  .bg-#{$key}-#{$i  * 20} { background-color: mix($value, white, $percentage);}
	}
}
