.accordion {
	padding: 15px 0;

	.ui-accordion-header {
		padding: 10px 30px 10px 0;
		margin: 0 20px;
		background: transparent !important;
		border: none;
		border-top: 1px solid #e0e2e3;
		font: normal 18px/22px $main-font;

		@include respond-to("small") {
			margin: 0 10px;
		}

		.text-main {
			color: $text-color;

			&:hover {
				color: $link-color;
			}
		}

		&:first-child {
			border-top: none;
		}

		// opened
		.ui-icon-triangle-1-s {
			@extend .icons-arrow-up;
		}
		// closed
		.ui-icon-triangle-1-e {
			@extend .icons-arrow-down;
		}

		.ui-icon-triangle-1-s,
		.ui-icon-triangle-1-e {
			&:before {
				font-size: 12px;
			}
		}
	}

	.ui-accordion-content {
		padding: 0 0 20px 0;
		color: $text-color;
		background: transparent;
		border: none;
		font: normal 14px/22px $basic-font;

		@include respond-to("small") {
			padding-bottom: 10px;
		}
	}

	.ui-state-focus {
		outline: none;
	}

	.ui-icon {
		background: transparent !important;
		text-indent: 0;
		overflow: initial;
		left: auto !important;
		right: 0;
		font-size: 0.4em;
		color: $text-primary;
	}

	.ui-corner-all {
		border-radius: 0 !important;
	}

	&.accordion-unique {
		padding-bottom: 0;

		.ui-accordion-header {
			border-bottom: 1px solid #e0e2e3
		}
	}
}
