$sub-menu-height: 200px;
$sec-menu-height: $sub-menu-height;
$thr-menu-height: $sec-menu-height - 20px;
$font-size: 18px;

.main-menu-wrapper {
	position: relative;
	height: 40px;
	width: 100%;
	// float: left;
	margin-bottom: 4px;
	background: url(../img/main-menu-bg.png) repeat-x $content-color;
	z-index: 1000;
}

.wrapper-full--nav {
	.main-menu-wrapper {
		background: none;
	}

	.form-search-activate {
		&.active {
			background: $base;
		}
	}

	.main-navigation .main-navigation-item {
		& > a {
			border-bottom: 0;
		}
		&:hover > a {
			background: $main-menu-bg;
		}
	}

	.main-navigation-item.active {
		background-color: $main-menu-active;

		> a {
			color: #FFF;

			@include respond-to("up-than-large") {
				height: 40px;
			}
		}
	}
}

.main-navigation {
	list-style: none;
	margin: 0;
	padding: 0;
	@include respond-to("large") {
		display: none;
		overflow: hidden;
		&.active {
			display: block;
		}
	}

	ul {
		list-style: none;
		margin: 0;
		padding: 0;
		font-size: 15px;
	}
	.main-navigation-item {
		float: left;
		margin-right: 1px;
		&:hover {
			.sub-menu-wrapper {
				display: block;
			}
			> a {
				color: white;
				background: url(../img/main-menu-bg-active.png) repeat-x $main-menu-bg;
				i {
					@include sprite($icons-main-menu-contract);
					display: none;
				}
			}
		}
		> a {
			display: block;
			height: 44px;
			font-weight: normal;
			font-size: 18px;
			font-family: $main-font;
			line-height: 40px;
			color: $navegation-color;
			padding: 0 19px 0 20px;
			text-decoration: none;
			text-align: center;
			position: relative;

			@include respond-to("large") {
				font-size: 18px;
			}
			&:after {
				display: inline-block;
				position: absolute;
				top: 12px;
				right: -1px;
				content: "";
				width: 1px;
				height: 12px;
				background-color: #a1a1a1;
			}
			i {
				@include sprite($icons-main-menu-expand);
				display: none;
			}
		}

	}
	.meta-navigation {
		display: none;
		@include respond-to(large) {
			display: block;
			ul {
				padding-left: 0;
				padding-top: 10px;
  				padding-bottom: 10px;
				a {
					font-size: 14px;
					line-height: 2;
				}
				.icons-internal-link,
				.icons-external-link {
					position: static;
					display: inline-block;
				}
			}
			&:hover {
				a > i,
				i {
					display: inline-block;
					background-position: 0px -402px;
					width: 11px;
				}

				.icons-external-link {
					@extend .icons-external-link;
					display: inline-block;
				}
			}

			a:hover {
				color: $link-color;
			}
		}
	}
}

.main-navigation--lang {
	display: none;
	&.active {
		display: block;
	}
}


@media (max-width: 845px) {
	.main-navigation {
		.main-navigation-item {
			> a {
				padding: 0 9px 0 10px;
			}
		}
	}
}

.sub-menu-wrapper {
	display: none;
	position: absolute;
	top: 40px;
	width: 100%;
	min-height: $sec-menu-height;
	padding: 20px 0;
	background-color: $main-menu-bg;
	z-index: 10;
	&.col-xl-12 {
		left: 0;
	}
}

.sub-menu-wrapper .sub-menu {
	padding: 0;
	@extend .unstyled;
	> li, > li.active {
		display: block;
		min-height: 38px;
		&:hover {
			> a {
				text-decoration: none;
				@include respond-to("up-than-large") {
					background-color: $link-color;
					background-image: none;
					i {
						color: $main-menu-arrow-active;
					}
				}
			}
			.sub-menu-sec-level {
				display: block;
				height: 100%;
				background-color: $main-menu-bg;
				z-index: 1;
			}
		}

		> a {
			position: relative;
			display: block;
			height: 100%;
			color: white;
			padding: 12px 40px 12px 20px;
			text-align: left;
			font-weight: normal;
			font-size: 14px;
			font-family: $main-font;
			z-index: 2;
			@include respond-to("large") {
				font-size: 18px;
				padding-top: 0;
				padding-bottom: 0;
			}
			&:after {
				display: none;
			}
			i {
				position: absolute;
				top: 12px;
				right: 16px;
				font-size: 16px;
				color: $main-menu-arrow;
			}
		}
		&.item-highlight {
			a {
				background-color: $item-highlight;
				&:hover {
					background-color: #98a2ad;
				}
			}
		}
	}
}
.sub-menu-wrapper .sub-menu {
	li.init-act {
		> a {
			background-color: $link-color;
			background-image: none;
			text-decoration: none;
			i {
				color: $main-menu-arrow-active;
			}
		}
		.sub-menu-sec-level {
			display: block;
		}

	}
}
.sub-menu-sec-level,
.sub-menu-third-level {
	display: none;
	position: absolute;
	top: 0;
	height: 100%;
	padding-top: 20px !important;
	@include respond-to("large") {
		position: static;
		padding-top: 0 !important;
	}
}

.sub-menu-sec-level {
	min-height: $sec-menu-height;
	@include respond-to("large") {
		min-height: 0;
		float: none;
	}
}

.sub-menu-third-level {
	left: 0;
	min-height: $thr-menu-height;
}

.main-menu-activate {
	display: none;
	position: relative;
	height: 40px;
	font: normal $font-size $main-font;
	line-height: 40px;
	color: $base;
	text-indent: 27px;
	padding-left: 5px;
	padding-right: 20px;
	&.active {
		color: white;
		background: url(../img/main-menu-bg-active.png) repeat-x $base;
		i {
			@include sprite($icons-main-menu-close);
			top: 14px;
			left: 10px;
		}
	}
	&:hover {
		text-decoration: none;
	}
	@include respond-to("large") {
		display: inline-block;
	}

	i {
		position: absolute;
		@include sprite($icons-main-menu-open);
		top: 15px;
		left: 12px;
	}
}

.nav-caller {
	display: none;
	font: normal $font-size $main-font;
	color: $base;
	padding-right: 20px;
	padding-left: 20px;
	line-height: 2.3;
	max-height: 40px;

	.icon-menu {
		@include sprite($icons-main-menu-open);
		display: inline-block;
		width: 13px; height: 13px;
	}

	i {
		display: inline-block;
		vertical-align: middle;
	}

	&.active {
		background: $main-menu-bg;
		color: white;

		.icon-menu {
			@include sprite($icons-main-menu-close);
			display: inline-block;
			top: 14px;
			left: 10px;
		}

		i {
			color: $link-color;
		}
	}
	&:hover, &.active, &:focus {
		text-decoration: none;
	}
	@include respond-to("large") {
		display: block;
		float: left;
	}
}

.floating {
	@include span(3 of $total-cols);
}

.floating-2 {
	@include span(7 of $total-cols);
}

.floating-3 {
	@include span(7 of $total-cols);

	@include respond-to("large") {
		@include span(6 of $total-cols);
	}
}

.floating, .floating-2, .floating-3 {
	margin-left: 0;
	min-height: 1px;
	@include respond-to("large") {
		width: 100%;
	}
}

// FLYOUTS

.flyout-1,
.flyout-2,
.flyout-3,
.flyout-4,
.flyout-5,
.flyout-6,
.flyout-7 {
	background-color: $main-menu-bg;
	padding-right: 0;
	padding-bottom: 20px;
	@include respond-to("large") {
		padding-bottom: 0;
	}
	/* > .row {
		border-left: 1px solid $main-menu-vl;
	} */
	.flyout-box-text-wrapper {
		font: normal 11px $basic-font;
		line-height: 1.5;
	}
	&.dark-bg .row p.col-xl-12 {
		margin-bottom: 0;
	}
	.flyout-submenu {
		background-color: $main-menu-bg;
		position: relative;
		z-index: 9;
	}
}

.flyout-1,
.flyout-2,
.flyout-3,
.flyout-7 {
	@include prefix(3 of $total-cols);
}

.flyout-4 {
	@include prefix(4 of $total-cols);
}

.flyout-5 {
	padding-bottom: 0;
}

.fixed-flyout {
	margin-top: 20px;
}

.flyout-items {
	padding: 0;
	.h5 {
		padding: 0 20px;
		margin: 0.56em 0;
		&:first-child {
			margin-top: 0;
		}
	}
	ul {
		padding-bottom: 20px;
	}
	a {
		display: block;
		padding: 4px 20px;
		min-height: 40px;
		line-height: 1.5 !important;
		&:hover {
			background-color: $link-color;
			text-decoration: none;
			.label-orange,
			.label-grey {
				background-color: white;
				color: $link-color;
			}
			.text-primary {
				color: white;
			}
		}
		span {
			display: block;
			&[class*="col-"] {
				padding: 0;
			}
			&.flyout-box-text-wrapper {
				padding-left: 10px;
			}

			&.text-nowrap {
				display: inline;
			}
		}
		.label {
			display: inline-block;
			width: 47px;
			text-align: center;
		}
	}

}

.sub-menu-sec-level .flyout-submenu {
	padding: 0;
	.sub-menu {
		li {
			&:hover {
				.sub-menu-third-level {
					display: block;
				}
			}
			/* @include respond-to("large") {
				a {
					margin-left: 20px;
				}
			} */
		}
	}
}

.flyout-box-list {
	li {
		background-color: $flyout-box-bg;
		margin-top: 20px;
		padding-left: 10px;
		.icons-internal-link {
			@include respond-to("large") {
				display: none !important;
			}
		}
		> [class*="col-"] {
			margin-left: 0 !important;
		}
		&:first-child {
			margin-top: 0;
		}
		.label,
		.flyout-box-text-wrapper {
			margin: 10px 0;
			a {
				padding: 0;
				min-height: 0;
				&:hover {
					background-color: transparent;
				}
				span {
					display: inline;
				}
			}
		}
		.label {
			width: 40px;
			margin-right: 0;
			padding: 4px;
		}
		.flyout-box-text-wrapper {
			padding: 0 0 0 10px !important;
		}
		.flyout-box-text-wrapper:first-child {
			padding-left: 0 !important;
			@extend .col-xl-8;
		}
		.flyout-box-image-wrapper {
			text-align: right;
			img {
				width: 100%;
			}
		}
		p {
			margin: 0;
		}
	}

}

.flyout-5 {
	.flyout-box-list {
		li {
			margin-bottom: 4px;

		}
		li:last-child {
			margin-bottom: 0;
		}
		.flyout-box-text-wrapper:first-child {
			font-size: 13px
		}

	}
}

.flyout-6 {
	.flyout-box-list {
		li {
			padding-left: 0;
		}

		.flyout-box-text-wrapper {
			padding-left: 10px !important;
			padding-right: 10px !important;
		}

		a:hover {
			color: $link-color;
			text-decoration: none;
		}
	}
}

.flyout-link {
	padding: 0 !important;

	&:hover {
		p {
			color: $amber;
		}
	}
}

.flyout-7 {
	padding-left: 41.43062%;
	@include respond-to("large") {
		padding-left: 0;
	}
}

.flyout-item-detached {
	clear: both;
	position: relative;
	left: 0;
	top: 20px;
	padding: 0 20px 20px 55px;
	margin-left: 0;
	color: white;
	@include span(3 of $total-cols);
	i {
		position: absolute;
		top: 0;
		left: 15px;
	}
	.flyout-box-text-wrapper {
		span {
			display: block;
		}
	}
}

@include ie8 {
	.sub-menu-sec-level {
		.flyout-1 {
			.flyout-items {
				li a {
					min-height: 30px;
				}
			}
		}
	}
}

// SMARTPHONE

.main-menu-activate {
	float: none;
}

.main-navigation {
	@include respond-to("large") {
		position: relative;
		top: 0;
		background: $main-menu-bg;
		z-index: 2000;
		width: 100%;
		.main-navigation-item
		//,.language-navigation-item
		 {
			background-image: none;
			position: relative;
			float: none;
			margin-right: 0;
			&:before,
			&:after {
				content: " ";
				/* 1 */
				display: table;
				/* 2 */
			}
			&:after {
				clear: both;
			}
			&.no-hover {
				&.active {
					.sub-menu-wrapper {
						display: none;
					}
				}
			}
			&:hover, &:hover > a {
				background-image: none;
				i {
					display: block;
					@include sprite($icons-main-menu-expand);
				}
				.sub-menu-wrapper {
					display: none;
				}
			}
			&.active, &.active > a {
				//height: auto;
				//background-color: $link-color;
				i {
					display: block;
					@include sprite($icons-main-menu-contract);
					top: 21px;
				}
				.sub-menu-wrapper {
					display: block;
				}
			}
			a {
				color: white;
				font: normal 18px $main-font;
				text-align: left;
				line-height: 44px;
				padding: 0 14px;
				&:after {
					display: none;
				}
				i {
					display: block;
					position: absolute;
					right: 15px;
					top: 15px;
				}

			}
			.sub-menu-wrapper {
				position: relative;
				top: auto;
				padding: 0;
				&.floating {
					width: 100%;
				}
				.sub-menu {
					li {
						/* background-color: $link-color; */
						border-bottom: 1px solid #333333;
						color: white;
						a {
							padding-left: 32px;
							i {
								display: none;
							}
							&:before {
								content: "";
								position: absolute;
								top: 17px;
								left: 14px;
								@include sprite($icons-main-menu-arrow-md);
							}
						}

						.lk-icon:hover {
							color: $link-color;
						}
					}
				}
				.sub-menu-sec-level {
					.sub-menu li:last-child {
						border-bottom: none;
					}
					.sub-menu {
						li a {
							padding-left: 50px;
							&:before {
								left: 34px;
							}
						}
					}
				}
			}
		}
	}
}

.form-search-activate {
	display: none;
	position: absolute;
	top: 0;
	right: 0;
	width: 54px;
	height: 40px;
	text-align: center;

	&.active {
		position: absolute;
		background: url(../img/main-menu-bg-active.png) repeat-x $base;
		i {
			@extend .icons-close;
			&:before {
				line-height: 40px;
			}
		}
	}

	@include respond-to("large") {
		display: block;
	}

	i {
		@extend .icons-loup;
		font-size: 16px;

		&:before {
			line-height: 40px;
		}
	}
}
